<script>
import qs from 'qs'
import $ from 'jquery'
import boyTeacher from '@/assets/img/boy_teacher.jpg'
import girlTeacher from '@/assets/img/girl_teacher.jpg'
import boyStudent from '@/assets/img/boy_student.jpg'
import girlStudent from '@/assets/img/girl_student.jpg'
import prove from '@/assets/img/prove.png'
import cardBg1 from '@/assets/img/lesson_card_bg1.jpg'
import cardBg2 from '@/assets/img/lesson_card_bg2.jpg'
import cardBg3 from '@/assets/img/lesson_card_bg3.jpg'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
import TimeSelector from '../Plugins/TimeSelector.vue'
export default {
  name: 'Info',
  components: {
    TimeSelector
  },
  metaInfo () {
    return {
      title: this.appName + '｜會員中心'
    }
  },
  data: function () {
    return {
      pageView: false,
      timeSelectorView: false,
      type: '',
      boyTeacher,
      girlTeacher,
      boyStudent,
      girlStudent,
      prove,
      cardBg1,
      cardBg2,
      cardBg3,
      loading,
      loadingStatus: false,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      infoPhoto: '',
      infoName: '',
      infoNickname: '',
      infoNicknameMessages: '',
      infoMobile: '',
      infoMobileMessages: '',
      infoYear: '',
      infoYearMessages: '',
      infoEmail: '',
      infoGender: '',
      infoBirth: '',
      infoRole: '',
      infoEdu1: '',
      infoEdu1Messages: '',
      infoEdu2: '',
      infoEdu2Messages: '',
      infoEdu3: '',
      infoEdu3Messages: '',
      infoJob: '',
      infoJobMessages: '',
      infoExp: '',
      infoExpMessages: '',
      infoIntro: '',
      infoIntroMessages: '',
      infoTag: '',
      infoTagMessages: '',
      infoNotifiemail: '',
      infoNotifiemailMessages: '',
      infoSelectTime: '',
      infoHeadshot: [
        {
          file: '',
          name: '',
          url: ''
        }
      ],
      uploadIMG: [
        {
          file: '',
          name: '',
          url: ''
        }, {
          file: '',
          name: '',
          url: ''
        }
      ],
      resLessonFavoritesList: []
    }
  },
  computed: {
    ...mapState(['root', 'snapshot', 'appName', 'dologinView', 'doResetPwdView']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  watch: {
    infoNickname (val) {
      if (val === '') {
        this.infoNicknameMessages = '請輸入您的暱稱'
      } else {
        this.infoNicknameMessages = '✔'
      }
    },
    infoMobile (val) {
      let isMobile = /^09[0-9]{2}-[0-9]{6}$/
      if (val === '') {
        this.infoMobileMessages = '✖ 手機號碼不得為空，格式為09XX-XXXXXX'
      } else if (!isMobile.test(this.infoMobile)) {
        this.infoMobileMessages = '✖ 手機號碼輸入格式錯誤，格式為09XX-XXXXXX'
      } else {
        this.infoMobileMessages = '✔'
      }
    },
    infoYear (val) {
      if (val === '') {
        this.infoYearMessages = '✖ 請選擇您的教學年資'
      } else {
        this.infoYearMessages = '✔'
      }
    },
    infoIntro (val) {
      if (val.length > 150) {
        this.infoIntroMessages = '✖ ' + val.length + '/150字'
      } else if (val.length === 0) {
        this.infoIntroMessages = '✖ ' + val.length + '/150字'
      } else {
        this.infoIntroMessages = '✔ ' + val.length + '/150字'
      }
    },
    infoTag (val) {
      if (val.length > 500) {
        this.infoTagMessages = '✖ ' + val.length + '/500字'
      } else if (val.length === 0) {
        this.infoTagMessages = '✖ ' + val.length + '/500字'
      } else {
        this.infoTagMessages = '✔ ' + val.length + '/500字'
      }
    },
    infoNotifiemail (val) {
      if (val === '') {
        this.infoNotifiemailMessages = '請用分號『;』隔開Email，如：you@cool.com;we@awesome.com'
      } else if (this.infoNotifiemailArray()) {
        this.infoNotifiemailMessages = '✖ Email輸入格式錯誤，格式為：you@cool.com;we@awesome.com'
      } else {
        this.infoNotifiemailMessages = '✔'
      }
    },
    infoNotifiemailArray (val) {
      let error = false
      let isNotifiemail = /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-|_)[A-Za-z0-9]+)*\.[A-Za-z]+$/
      let list = this.infoNotifiemail.split(';')
      list.forEach((l) => {
        if (!isNotifiemail.test(l)) {
          error = true
        }
      })
      return error
    },
    infoEdu1 () {
      this.infoEdu1Verify()
      this.infoEdu2Verify()
      this.infoEdu3Verify()
      this.infoJobVerify()
      this.infoExpVerify()
    },
    infoEdu2 () {
      this.infoEdu1Verify()
      this.infoEdu2Verify()
      this.infoEdu3Verify()
      this.infoJobVerify()
      this.infoExpVerify()
    },
    infoEdu3 () {
      this.infoEdu1Verify()
      this.infoEdu2Verify()
      this.infoEdu3Verify()
      this.infoJobVerify()
      this.infoExpVerify()
    },
    infoJob () {
      this.infoEdu1Verify()
      this.infoEdu2Verify()
      this.infoEdu3Verify()
      this.infoJobVerify()
      this.infoExpVerify()
    },
    infoExp () {
      this.infoEdu1Verify()
      this.infoEdu2Verify()
      this.infoEdu3Verify()
      this.infoJobVerify()
      this.infoExpVerify()
    }
  },
  mounted () {
    if (localStorage.getItem('token')) {
      let body = {
        userId: this.userInfo ? this.userInfo.user_id : -1,
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              if (this.$route.params.type) {
                this.type = this.$route.params.type
              }
              if (this.$route.params.type === this.userInfo.type) {
                if (this.userInfo.type === 'teacher') {
                  this.getTeacherInfo()
                } else if (this.userInfo.type === 'student') {
                  this.getStudentInfo()
                }
              } else {
                this.$router.push('/')
              }
            } else {
              this.$router.push('/')
            }
          } else {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else {
      this.$router.push('/')
    }
  },
  methods: {
    test () {
      alert('未開放')
    },
    getTeacherInfo () {
      this.infoHeadshot = [
        {
          file: '',
          name: '',
          url: ''
        }
      ]
      this.uploadIMG = [
        {
          file: '',
          name: '',
          url: ''
        }, {
          file: '',
          name: '',
          url: ''
        }
      ]
      this.$http.get(`/api/account/returnTeacherProfileForOneselfViewing/?userID=${this.userInfo.user_id}`).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.pageView = true
            // res.data.id_approved
            this.infoEdu1 = res.data.education_1
            this.infoEdu2 = res.data.education_2
            this.infoEdu3 = res.data.education_3
            // res.data.education_approved
            this.infoJob = res.data.company
            // res.data.work_approved
            this.infoExp = res.data.special_exp
            // res.data.other_approved
            // res.data.is_approved
            this.infoName = res.data.name
            this.infoNickname = res.data.nickname
            this.infoEmail = res.data.username
            this.infoMobile = res.data.mobile
            this.infoBirth = res.data.birth_date
            this.infoYear = res.data.tutor_experience
            this.infoIntro = res.data.intro
            this.infoTag = res.data.subject_type
            this.infoGender = res.data.is_male ? '1' : '0'
            if (res.data.thumbnail_dir !== '') {
              this.infoPhoto = res.data.thumbnail_dir + '?t=' + new Date().getTime()
            } else {
              if (res.data.is_male) {
                this.infoPhoto = this.boyTeacher
              } else {
                this.infoPhoto = this.girlTeacher
              }
            }
            this.infoSelectTime = res.data.general_available_time
            this.timeSelectorView = true
            // res.data.balance: 0
            // res.data.unearned_balance: 0
            // res.data.withholding_balance: 0
            let user = JSON.parse(localStorage.getItem('user'))
            user.nickname = this.infoNickname
            user.picture = this.infoPhoto
            this.$store.state.snapshot = this.infoPhoto
            localStorage.setItem('user', JSON.stringify(user))
            // this.getLessonFavoritesList()
          } else {
            this.$router.push('/')
          }
        })
    },
    getStudentInfo () {
      this.infoHeadshot = [
        {
          file: '',
          name: '',
          url: ''
        }
      ]
      this.$http.get(`/api/account/returnStudentProfileForOneselfViewing/?userID=${this.userInfo.user_id}`).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.pageView = true
            this.infoName = res.data.name
            this.infoNickname = res.data.nickname
            this.infoEmail = res.data.username
            this.infoMobile = res.data.mobile
            this.infoBirth = res.data.birth_date
            this.infoIntro = res.data.intro
            this.infoRole = res.data.role
            this.infoNotifiemail = res.data.update_someone_by_email
            this.infoGender = res.data.is_male ? '1' : '0'
            if (res.data.thumbnail_dir !== '') {
              this.infoPhoto = res.data.thumbnail_dir + '?t=' + new Date().getTime()
            } else {
              if (res.data.is_male) {
                this.infoPhoto = this.boyStudent
              } else {
                this.infoPhoto = this.girlStudent
              }
            }
            // res.data.balance: 0
            // res.data.withholding_balance: 0
            let user = JSON.parse(localStorage.getItem('user'))
            user.nickname = this.infoNickname
            user.picture = this.infoPhoto
            this.$store.state.snapshot = this.infoPhoto
            localStorage.setItem('user', JSON.stringify(user))
            this.getLessonFavoritesList()
          } else {
            this.$router.push('/')
          }
        })
    },
    getLessonFavoritesList () {
      this.loadingStatus = true
      let body = {
        userID: this.userInfo ? this.userInfo.user_id : -1,
        qty: -1,
        keywords: '',
        only_show_ones_favorites: true,
        only_show_lessons_by_this_teacher_s_auth_id: -1,
        ordered_by: '',
        filtered_by: ''
      }
      this.$http.post('/api/lesson/getLessonCardsForCommonUsers/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          this.loadingStatus = false
          if (res.status === 'success') {
            this.resLessonFavoritesList = res.data
          } else {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          }
        })
    },
    doResetPwd () {
      this.$store.state.doResetPwdView = true
    },
    doFavorite (id, status) {
      if (localStorage.getItem('token')) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          lessonID: id,
          status: status
        }
        this.$http.post('/api/lesson/addOrRemoveFavoriteLessons/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.getLessonFavoritesList()
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已' + (status ? '加入' : '移除') + '收藏'
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    doSubmitTeacher (action) {
      if (this.infoNicknameMessages.indexOf('✖') === -1 &&
      this.infoMobileMessages.indexOf('✖') === -1 &&
      this.infoYearMessages.indexOf('✖') === -1 &&
      this.infoIntroMessages.indexOf('✖') === -1 &&
      this.infoTagMessages.indexOf('✖') === -1 &&
      this.infoEdu1Messages.indexOf('✖') === -1 &&
      this.infoEdu2Messages.indexOf('✖') === -1 &&
      this.infoEdu3Messages.indexOf('✖') === -1 &&
      this.infoJobMessages.indexOf('✖') === -1 &&
      this.infoExpMessages.indexOf('✖') === -1) {
        let formData = new FormData()
        formData.append('userID', this.userInfo ? this.userInfo.user_id : -1)
        formData.append('type', 'teacher')
        formData.append('nickname', this.infoNickname || this.infoName)
        formData.append('intro', this.infoIntro)
        formData.append('mobile', this.infoMobile)
        formData.append('tutor_experience', this.infoYear)
        formData.append('subject_type', this.infoTag)
        formData.append('education_1', this.infoEdu1)
        formData.append('education_2', this.infoEdu2)
        formData.append('education_3', this.infoEdu3)
        formData.append('upload_snapshot', this.infoHeadshot[0].file)
        formData.append('company', this.infoJob)
        formData.append('special_exp', this.infoExp)
        formData.append('teacher_general_availabale_time', this.resInfoSelectTime)
        this.uploadIMG.forEach((up) => {
          formData.append('upload_cer', up.file)
        })
        this.$http.post('/api/account/editTeacherProfile/', formData).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.timeSelectorView = false
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已成功修改資料'
              scroll(0, 0)
              setTimeout(() => {
                this.getTeacherInfo()
              }, 500)
              setTimeout(() => {
                if (action === 'preview') {
                  this.$router.push('/account/profile/' + this.userInfo.user_id)
                }
              }, 1000)
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.submit = false
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '請再次檢查欄位是否填妥'
      }
    },
    doSubmitStudent () {
      if (this.infoNicknameMessages.indexOf('✖') === -1 &&
      this.infoMobileMessages.indexOf('✖') === -1 &&
      this.infoNotifiemailMessages.indexOf('✖') === -1) {
        let formData = new FormData()
        formData.append('userID', this.userInfo ? this.userInfo.user_id : -1)
        formData.append('type', 'student')
        formData.append('nickname', this.infoNickname || this.infoName)
        formData.append('intro', this.infoIntro)
        formData.append('mobile', this.infoMobile)
        formData.append('update_someone_by_email', this.infoNotifiemail)
        formData.append('upload_snapshot', this.infoHeadshot[0].file)
        this.uploadIMG.forEach((up) => {
          formData.append('upload_cer', up.file)
        })
        this.$http.post('/api/account/editStudentProfile/', formData).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已成功修改資料'
              scroll(0, 0)
              setTimeout(() => {
                this.getStudentInfo()
              }, 500)
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.submit = false
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '請再次檢查欄位是否填妥'
      }
    },
    resSelectTime (val) {
      this.resInfoSelectTime = val
    },
    uploadHeadshot () {
      if (!this.infoHeadshot[0].url) {
        $('#headshot-file').click()
      }
    },
    cancelHeadshot () {
      this.infoHeadshot[0].file = this.infoHeadshot[0].name = this.infoHeadshot[0].url = ''
      $('#headshot-file').val('')
    },
    setHeadshot (e) {
      let files = e.target.files || e.dataTransfer.files
      if (e.srcElement.files.item(0).size > (4 * 1024 * 1024)) {
        $('#headshot-file').val('')
        alert('上傳照片不得超過4MB')
      } else {
        // alert('Uploading: ' + e.srcElement.files.item(0).name + ' @ ' + e.srcElement.files.item(0).size + 'bytes')
        // console.log(window.URL.createObjectURL(e.srcElement.files.item(0)))
        // console.log(e.srcElement.files.item(0).name)
        // let target = e.target
        // console.log(this.uploadIMG[i].url)
        // this.uploadIMG[i].url = this.getFileUrl(e.srcElement)
        this.infoHeadshot[0].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.infoHeadshot[0].file = files[0]
        this.infoHeadshot[0].name = e.srcElement.files.item(0).name
        // $('#img_' + target.id).attr('src', this.getFileUrl(e.srcElement))
      }
    },
    cancelHeadshotIMG () {
      this.infoHeadshot[0].file = this.infoHeadshot[0].name = this.infoHeadshot[0].url = ''
      $('#headshot-file').val('')
      // this.createImage(files[0])
    },
    setImg (e, i) {
      let files = e.target.files || e.dataTransfer.files
      if (e.srcElement.files.item(0).size > (10 * 1024 * 1024)) {
        $('#file' + i).val('')
        alert('每個上傳檔案不得超過10MB')
      } else {
        this.uploadIMG[i].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.uploadIMG[i].file = files[0]
        this.uploadIMG[i].name = e.srcElement.files.item(0).name
      }
    },
    cancelUploadIMG (i) {
      this.uploadIMG[i].file = this.uploadIMG[i].name = this.uploadIMG[i].url = ''
      $('#file' + i).val('')
    },
    addUploadItem () {
      this.uploadIMG.push({ file: '', name: '', url: '' })
    },
    infoEdu1Verify () {
      if (this.infoEdu1 === '' && this.infoEdu2 === '' && this.infoEdu3 === '' && this.infoJob === '' && this.infoExp === '') {
        this.infoEdu1Messages = '✖ 請至少輸入一項學經歷。'
      } else if (this.infoEdu1 === '') {
        this.infoEdu1Messages = '請輸入第一學歷/科系，如正在就讀中請加寫年級。'
      } else if (this.infoEdu1 !== '') {
        this.infoEdu1Messages = '✔'
      }
    },
    infoEdu2Verify () {
      if (this.infoEdu1 === '' && this.infoEdu2 === '' && this.infoEdu3 === '' && this.infoJob === '' && this.infoExp === '') {
        this.infoEdu2Messages = '✖ 請至少輸入一項學經歷。'
      } else if (this.infoEdu2 === '') {
        this.infoEdu2Messages = '請輸入第二學歷/科系，如正在就讀中請加寫年級。'
      } else if (this.infoEdu2 !== '') {
        this.infoEdu2Messages = '✔'
      }
    },
    infoEdu3Verify () {
      if (this.infoEdu1 === '' && this.infoEdu2 === '' && this.infoEdu3 === '' && this.infoJob === '' && this.infoExp === '') {
        this.infoEdu3Messages = '✖ 請至少輸入一項學經歷。'
      } else if (this.infoEdu3 === '') {
        this.infoEdu3Messages = '請輸入第三學歷/科系，如正在就讀中請加寫年級。'
      } else if (this.infoEdu3 !== '') {
        this.infoEdu3Messages = '✔'
      }
    },
    infoJobVerify () {
      if (this.infoEdu1 === '' && this.infoEdu2 === '' && this.infoEdu3 === '' && this.infoJob === '' && this.infoExp === '') {
        this.infoJobMessages = '✖ 請至少輸入一項學經歷。'
      } else if (this.infoJob === '') {
        this.infoJobMessages = '請輸入現任單位/職稱。'
      } else if (this.infoJob !== '') {
        this.infoJobMessages = '✔'
      }
    },
    infoExpVerify () {
      if (this.infoEdu1 === '' && this.infoEdu2 === '' && this.infoEdu3 === '' && this.infoJob === '' && this.infoExp === '') {
        this.infoExpMessages = '✖ 請至少輸入一項學經歷。'
      } else if (this.infoExp === '') {
        this.infoExpMessages = '請輸入其他學經歷或特殊專長。'
      } else if (this.infoExp !== '') {
        this.infoExpMessages = '✔'
      }
    }
  }
}
</script>
