<template>
  <v-content id="lessonSearch">
    <div class="container">
      <div class="page-search-area d-flex d-lg-none d-xl-none">
        <input
          id="page-search-input"
          v-model="searchKeyword"
          class="row-btn-50"
          placeholder="請輸入科目或老師"
          @keyup.enter="doKeywordSearch()">
        <button
          class="page-search-btn btn-50"
          @click="doKeywordSearch()">
          <v-icon>mdi-magnify</v-icon>
        </button>
      </div>
      <div class="row">
        <div class="filter-bar col-xs-12 col-md-12 my-2 border-bottom">
          <v-chip
            :class="{'active': filterBar==='best_sales'}"
            class="cursor-no-hover h5"
            @click="filterBar = (filterBar==='best_sales'?'':'best_sales')">熱門優先</v-chip>
          <v-chip
            v-if="filterBar!=='reach_rate_asc'"
            :class="{'active': filterBar==='reach_rate_desc'}"
            class="cursor-no-hover h5 "
            @click="filterBar = (filterBar==='reach_rate_desc'?'reach_rate_asc':'reach_rate_desc')">完課量<v-icon>mdi-menu-down</v-icon></v-chip>
          <v-chip
            v-if="filterBar==='reach_rate_asc'"
            :class="{'active': filterBar==='reach_rate_asc'}"
            class="cursor-no-hover h5 "
            @click="filterBar = (filterBar==='reach_rate_asc'?'':'reach_rate_asc')">完課量<v-icon>mdi-menu-up</v-icon></v-chip>
          <v-chip
            v-if="filterBar!=='price_per_hour_asc'"
            :class="{'active': filterBar==='price_per_hour_desc'}"
            class="cursor-no-hover h5"
            @click="filterBar = (filterBar==='price_per_hour_desc'?'price_per_hour_asc':'price_per_hour_desc')">鐘點費<v-icon>mdi-menu-down</v-icon></v-chip>
          <v-chip
            v-if="filterBar==='price_per_hour_asc'"
            :class="{'active': filterBar==='price_per_hour_asc'}"
            class="cursor-no-hover h5"
            @click="filterBar = (filterBar==='price_per_hour_asc'?'':'price_per_hour_asc')">鐘點費<v-icon>mdi-menu-up</v-icon></v-chip>
          <v-chip
            v-if="filterBar!=='lesson_scores_asc'"
            :class="{'active': filterBar==='lesson_scores_desc'}"
            class="cursor-no-hover h5"
            @click="filterBar = (filterBar==='lesson_scores_desc'?'lesson_scores_asc':'lesson_scores_desc')">評價<v-icon>mdi-menu-down</v-icon></v-chip>
          <v-chip
            v-if="filterBar==='lesson_scores_asc'"
            :class="{'active': filterBar==='lesson_scores_asc'}"
            class="cursor-no-hover h5"
            @click="filterBar = (filterBar==='lesson_scores_asc'?'':'lesson_scores_asc')">評價<v-icon>mdi-menu-up</v-icon></v-chip>
          <div
            class="cursor-no-hover moreFilter main-color"
            @click="moreFilterView=true">
            <v-icon class="toolbar-icon">mdi-filter</v-icon>
            <span class="xs-font-14">更多篩選</span>
          </div>
        </div>
      </div>
      <div
        :class="{'loading':loadingStatus}"
        class="row">
        <p
          v-if="!loadingStatus&&resLessonSearchList.length===0"
          class="text-center w-100">無符合條件的課程</p>
        <div
          v-for="(card, c) in resLessonSearchList"
          :key="'c'+c"
          class="item-cube col-xs-12 col-md-6 my-2">
          <div
            :style="'background-image: url(\'' + root + ((card.teacher_thumbnail_path!=='')?card.teacher_thumbnail_path:(card.is_this_teacher_male?boyTeacher:girlTeacher)) + '\');'"
            class="teacher-photo"/>
          <div
            v-if="card.best_sale!==''"
            class="best_sale">{{ card.best_sale }}</div>
          <v-layout
            item-cube-main
            justify-center>
            <v-flex
              :style="'background-image: url(\'' + root + ((card.background_picture_code===99)?card.background_picture_path:((card.background_picture_code===0)?cardBg1:(card.background_picture_code===1)?cardBg2:cardBg3)) + '\');'"
              class="top">
              <div :style="'color:'+card.title_color+';'">{{ card.big_title }}</div>
              <div :style="'color:'+card.title_color+';'">{{ card.little_title }}</div>
              <v-icon
                v-if="!card.is_this_my_favorite_lesson"
                class="cursor"
                style="color: coral;"
                @click="doFavorite(card.lessonID,true)">mdi-heart-outline</v-icon>
              <v-icon
                v-if="card.is_this_my_favorite_lesson"
                class="cursor"
                style="color: coral;"
                @click="doFavorite(card.lessonID,false)">mdi-heart</v-icon>
            </v-flex>
            <v-flex class="middle">
              <p class="fraction md-font-18 xs-font-10 word-keep-all">評價{{ card.lesson_avg_score }} ({{ card.lesson_reviewed_times }})</p>
              <span
                class="xs-font-18 md-font-25 float-left word-keep-all cursor"
                @click="$router.push('/account/profile/'+card.teacher_auth_id)">{{ card.teacher_nickname }}</span>
              <span class="xs-font-18 md-font-25 float-right word-keep-all">鐘點費${{ card.price_per_hour }}</span>
            </v-flex>
            <v-flex class="bottom">
              <div class="text-left mb-2">
                <p
                  class="title mb-2 cursor"
                  @click="$router.push('/lesson/main/view/'+card.lessonID)">{{ card.lesson_title }}</p>
                <p class="prove">
                  <img
                    v-if="card.education_is_approved"
                    :src="prove">{{ card.education }}</p>
                <p class="prove">
                  <img
                    v-if="card.working_experience_is_approved"
                    :src="prove">{{ card.working_experience }}</p>
              </div>
              <div class="text-left">
                <span
                  v-if="card.highlight_1!==''"
                  class="tag md-font-13 xs-font-9 word-keep-all">{{ card.highlight_1 }}</span>
                <span
                  v-if="card.highlight_2!==''"
                  class="tag md-font-13 xs-font-9 word-keep-all">{{ card.highlight_2 }}</span>
                <span
                  v-if="card.highlight_3!==''"
                  class="tag md-font-13 xs-font-9 word-keep-all">{{ card.highlight_3 }}</span>
              </div>
            </v-flex>
          </v-layout>
        </div>
      </div>
    </div>
    <v-dialog
      v-model="moreFilterView"
      max-width="1110px"
      width="100%"
      persistent>
      <!-- <template v-slot:activator="{ on }"/> -->
      <v-card
        max-width="100%"
        style="margin:0 auto;">
        <v-card-text class="p-0">
          <v-alert
            v-model="moreFilterView"
            class="border-0"
            color="bg-main-color">
            <strong class="h4">篩選條件</strong>
            <!-- <v-btn
              class="title-btn bg-white-color"
              @click="doMorefilterOpenAll(true)">
              展開
            </v-btn>
            <v-btn
              class="title-btn bg-white-color"
              @click="doMorefilterOpenAll(false)">
              收合
            </v-btn> -->
            <v-icon
              class="cursor float-right text-white"
              @click="moreFilterView=false">mdi-close</v-icon>
          </v-alert>
        </v-card-text>
        <v-card-actions style="padding: 0;justify-content: center;flex-flow: column;">
          <div
            v-for="(filter, f) in filterSelectList"
            :key="'filter' + f"
            class="filter-bar row col-xs-12 col-md-12 my-2 p-0 justify-start">
            <div
              class="cursor scroll col-xs-12 col-md-12 p-0"
              @click="filterSelectOpenList[f].open=!filterSelectOpenList[f].open">
              <span class="title pr-2">{{ filter.title }}</span>
              <span class="line"/>
              <span class="switch pl-2">
                <v-icon v-if="filterSelectOpenList[f].open===false">mdi-chevron-down</v-icon>
                <v-icon v-if="filterSelectOpenList[f].open===true">mdi-chevron-up</v-icon>
              </span>
            </div>
            <div
              :class="{'filter-bar-area-open': filterSelectOpenList[f].open===true}"
              class="filter-bar-area col-xs-12 col-md-12 p-0">
              <v-chip
                v-for="(item, i) in filter.items"
                :key="'item' + f + i"
                :class="{'active': filterSelectList[f].items[i].select===true}"
                class="cursor-no-hover h5"
                @click="filterSelectList[f].items[i].select=!filterSelectList[f].items[i].select">{{ item.text }}</v-chip>
            </div>
          </div>
          <div
            class="filter-bar row col-xs-12 col-md-12 my-2 p-0 justify-start">
            <div
              class="cursor scroll col-xs-12 col-md-12 p-0"
              @click="filterSelectOpenList[4].open=!filterSelectOpenList[4].open">
              <span class="title pr-2">鐘點費區間</span>
              <span class="line"/>
              <span class="switch pl-2">
                <v-icon v-if="filterSelectOpenList[4].open===false">mdi-chevron-down</v-icon>
                <v-icon v-if="filterSelectOpenList[4].open===true">mdi-chevron-up</v-icon>
              </span>
            </div>
            <div
              :class="{'filter-bar-area-open': filterSelectOpenList[4].open===true}"
              class="filter-bar-area col-xs-12 col-md-12 p-0 text-center">
              <input
                v-model="filterSelectFee.min"
                :class="{'active': filterSelectFee.min!==0}"
                :min="0"
                :step="1"
                class="h5 item-input"
                type="number">
              <span class="mx-3">到</span>
              <input
                v-model="filterSelectFee.max"
                :class="{'active': filterSelectFee.max!==99999}"
                :min="filterSelectFee.min"
                :step="1"
                class="h5 item-input"
                type="number">
            </div>
          </div>
          <div class="row">
            <div class="col-xs-12 col-md-12 p-0 text-center">
              <v-btn
                class="mx-2 my-4 px-3 py-2 h5 bg-secondary"
                @click="getMorefilterList()">
                <v-icon class="mr-2">mdi-restore</v-icon>重設
              </v-btn>
              <v-btn
                class="mx-2 my-4 px-3 py-2 h5 bg-button-color"
                @click="doSearch()">
                <v-icon class="mr-2">mdi-check</v-icon>使用
              </v-btn>
            </div>
          </div>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar
      v-model="snackbar"
      :top="true"
      :color="color">
      {{ errorMessages }}
      <v-btn
        dark
        flat
        @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </v-content>
</template>

<script>
import qs from 'qs'
import boyTeacher from '@/assets/img/boy_teacher.jpg'
import girlTeacher from '@/assets/img/girl_teacher.jpg'
import prove from '@/assets/img/prove.png'
import cardBg1 from '@/assets/img/lesson_card_bg1.jpg'
import cardBg2 from '@/assets/img/lesson_card_bg2.jpg'
import cardBg3 from '@/assets/img/lesson_card_bg3.jpg'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
export default {
  name: 'LessonSearch',
  metaInfo () {
    return {
      title: this.appName + '｜課程' + ((this.searchKeyword !== '') ? ('(搜尋：' + this.searchKeyword + ')') : '')
    }
  },
  data: function () {
    return {
      boyTeacher,
      girlTeacher,
      prove,
      cardBg1,
      cardBg2,
      cardBg3,
      loading,
      loadingStatus: false,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      searchKeyword: '',
      filterBar: '',
      moreFilterView: false,
      resLessonSearchList: [],
      filterSelectList: [],
      filterSelectOpenList: [
        { open: true },
        { open: true },
        { open: true },
        { open: true },
        { open: true }
      ],
      filterSelectFee: {
        open: false,
        min: 0,
        max: 99999
      }
    }
  },
  computed: {
    ...mapState(['root', 'appName', 'dologinView']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    },
    filtered () {
      let select = ''
      this.filterSelectList.forEach((f) => {
        let array = []
        f.items.forEach((s) => {
          if (s.select) {
            array.push(s.value)
          }
        })
        if (array.length > 0) {
          select = select + f.type + ':' + array.join() + ';'
        }
      })
      select = select + 'filtered_price_per_hour:' + this.filterSelectFee.min + ',' + this.filterSelectFee.max
      return select
    }
  },
  watch: {
    '$route.fullPath': function (newValue) {
      this.$router.go(0)
    },
    filterBar () {
      this.resLessonSearchList = []
      this.doSearch()
    },
    'filterSelectFee.min': function (newValue) {
      this.filterSelectFee.min = Math.round(newValue)
      if (this.filterSelectFee.max < Math.round(newValue)) {
        this.filterSelectFee.max = Math.round(newValue)
      }
    },
    'filterSelectFee.max': function (newValue) {
      if (newValue === '') {
        this.filterSelectFee.max = 99999
      } else if (newValue < this.filterSelectFee.min) {
        this.filterSelectFee.max = this.filterSelectFee.min
      } else {
        this.filterSelectFee.max = Math.round(newValue)
      }
    }
  },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  mounted () {
    const { q } = this.$route.query
    this.searchKeyword = q || ''
    let publicPage = true
    if (localStorage.getItem('token') || publicPage) {
      let body = {
        userId: this.userInfo ? this.userInfo.user_id : -1,
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              this.getMorefilterList()
              this.doSearch()
            } else {
              // this.$router.push('/')
            }
          } else {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else {
      // this.$router.push('/')
    }
    // this.doSearch()
    // this.resLessonSearchList = [
    //   {
    //     id: 1,
    //     title: '番茄姐姐英文',
    //     introduction: '番茄姐姐英文課程介紹等等',
    //     img_url: ''
    //   }, {
    //     id: 2,
    //     title: '香蕉哥哥國文',
    //     introduction: '香蕉哥哥國文課程介紹等等',
    //     img_url: ''
    //   }
    // ]
  },
  methods: {
    doKeywordSearch () {
      this.doSearch()
      this.$router.push('/lesson/search?q=' + this.searchKeyword)
    },
    doSearch () {
      this.loadingStatus = true
      this.moreFilterView = false
      let body = {
        userID: this.userInfo ? this.userInfo.user_id : -1,
        qty: -1,
        keywords: this.searchKeyword,
        only_show_ones_favorites: false,
        only_show_lessons_by_this_teacher_s_auth_id: -1,
        ordered_by: this.filterBar,
        filtered_by: this.filtered
      }
      this.$http.post('/api/lesson/getLessonCardsForCommonUsers/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          this.loadingStatus = false
          if (res.status === 'success') {
            this.resLessonSearchList = res.data
            // this.resLessonSearchList = res.data.slice(0, 30)
            // setTimeout(() => {
            //   this.resLessonSearchList = Array.prototype.concat.apply(this.resLessonSearchList, res.data.slice(30, res.data.length))
            // }, 1000)
          } else {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          }
        })
    },
    test () {
      alert('未開放')
    },
    doFavorite (id, status) {
      if (localStorage.getItem('token')) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          lessonID: id,
          status: status
        }
        this.$http.post('/api/lesson/addOrRemoveFavoriteLessons/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.doSearch()
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已' + (status ? '加入' : '移除') + '收藏'
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    // doMorefilterOpenAll (val) {
    //   this.filterSelectOpenList.forEach((f) => {
    //     f.open = val
    //   })
    // },
    getMorefilterList () {
      this.filterSelectFee = {
        open: false,
        min: 0,
        max: 99999
      }
      this.filterSelectList = [
        {
          type: 'filtered_subjects',
          title: '科目',
          open: true,
          items: []
        },
        {
          type: 'filtered_target_students',
          title: '對象',
          open: false,
          items: []
        },
        {
          type: 'filtered_times',
          title: '上課時段',
          open: false,
          items: []
        },
        {
          type: 'filtered_tutoring_experience',
          title: '教學年資',
          open: false,
          items: []
        }
      ]
      this.$http.get(`/api/lesson/getALLFilteredKeysAndValues/`).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.filterSelectList[0].items = res.data.filtered_subjects
            this.filterSelectList[1].items = res.data.filtered_target_students
            this.filterSelectList[2].items = res.data.filtered_times
            this.filterSelectList[3].items = res.data.filtered_tutoring_experience
          }
        })
    }
  }
}

</script>
