<template>
  <v-content id="profile">
    <div class="container">
      <div class="row my-2 border-bottom">
        <div class="col-xs-12 col-md-4 teacher-profile-left">
          <div
            :style="'background-image: url(\'' + root + profilePhoto + '\');'"
            class="teacher-photo"/>
          <div class="teacher-name">
            <span><img :src="profileGender">{{ profileNickname }}</span>
            <!-- 評價保留區
            <span class="bg-button-color">評價{{ profileAvgScore }} ({{ profileReviewedTimes }})</span>
            -->
          </div>
          <span class="teacher-seniority bg-main-color">教學年資：{{ profileYear }}</span>
          <div class="prove-area">
            <p
              v-if="profileEdu1!==''"
              class="prove">
              <img
                v-if="profileEduApproved"
                :src="prove">{{ profileEdu1 }}</p>
            <p
              v-if="profileEdu2!==''"
              class="prove">
              <img
                v-if="profileEduApproved"
                :src="prove">{{ profileEdu2 }}</p>
            <p
              v-if="profileEdu3!==''"
              class="prove">
              <img
                v-if="profileEduApproved"
                :src="prove">{{ profileEdu3 }}</p>
            <p
              v-if="profileJob!==''"
              class="prove">
              <img
                v-if="profileJobApproved"
                :src="prove">{{ profileJob }}</p>
            <p
              v-if="profileExp!==''"
              class="prove">
              <img
                v-if="profileExpApproved"
                :src="prove">{{ profileExp }}</p>
          </div>
        </div>
        <div class="col-xs-12 col-md-8 teacher-profile-right">
          <div class="text-left">
            <p class="subtitle mb-2 xs-font-20 md-font-30">關於老師</p>
            <p class="content xs-font-15 md-font-20">{{ profileIntro||'老師尚未填寫相關資訊'  }}</p>
          </div>
          <div class="text-left">
            <p class="subtitle mb-2 xs-font-20 md-font-30">授課科目與對象</p>
            <p class="content xs-font-15 md-font-20">{{ profileTag||'老師尚未填寫相關資訊'  }}</p>
          </div>
          <v-btn
            v-if="userInfo?userInfo.type!=='teacher':false"
            class="my-3 mx-0 px-4 h5 bg-chat-color v-btn float-right theme--light"
            @click="doConnectChatroom()">
            <v-icon class="mr-2">mdi-comment-text-multiple</v-icon>聯絡老師
          </v-btn>
        </div>
      </div>
      <div
        :class="{'loading':loadingStatus}"
        class="row">
        <p
          v-if="!loadingStatus&&resLessonList.length===0"
          class="text-center w-100">尚無開設課程</p>
        <div
          v-for="(card, c) in resLessonList"
          :key="'c'+c"
          class="item-cube col-xs-12 col-md-6 my-2">
          <div
            :style="'background-image: url(\'' + root + ((card.teacher_thumbnail_path!=='')?card.teacher_thumbnail_path:(card.is_this_teacher_male?boyTeacher:girlTeacher)) + '\');'"
            class="teacher-photo"/>
          <div
            v-if="card.best_sale!==''"
            class="best_sale">{{ card.best_sale }}</div>
          <v-layout
            item-cube-main
            justify-center>
            <v-flex
              :style="'background-image: url(\'' + root + ((card.background_picture_code===99)?card.background_picture_path:((card.background_picture_code===0)?cardBg1:(card.background_picture_code===1)?cardBg2:cardBg3)) + '\');'"
              class="top">
              <div :style="'color:'+card.title_color+';'">{{ card.big_title }}</div>
              <div :style="'color:'+card.title_color+';'">{{ card.little_title }}</div>
              <!-- 評價保留區
              <v-icon
                v-if="!card.is_this_my_favorite_lesson"
                class="cursor"
                style="color: coral;"
                @click="doFavorite(card.lessonID,true)">mdi-heart-outline</v-icon>
              <v-icon
                v-if="card.is_this_my_favorite_lesson"
                class="cursor"
                style="color: coral;"
                @click="doFavorite(card.lessonID,false)">mdi-heart</v-icon>
              -->
            </v-flex>
            <v-flex class="middle">
              <!-- 評價保留區
              <p class="fraction md-font-18 xs-font-10 word-keep-all">評價{{ card.lesson_avg_score }} ({{ card.lesson_reviewed_times }})</p>
              -->
              <span
                class="xs-font-18 md-font-25 float-left word-keep-all cursor"
                @click="scrollTop()">{{ card.teacher_nickname }}</span>
              <span class="xs-font-18 md-font-25 float-right word-keep-all">鐘點費${{ card.price_per_hour }}</span>
            </v-flex>
            <v-flex class="bottom">
              <div class="text-left mb-2">
                <p
                  class="title mb-2 cursor"
                  @click="$router.push('/lesson/main/view/'+card.lessonID)">{{ card.lesson_title }}</p>
                <p class="prove">
                  <img
                    v-if="card.education_is_approved"
                    :src="prove">{{ card.education }}</p>
                <p class="prove">
                  <img
                    v-if="card.working_experience_is_approved"
                    :src="prove">{{ card.working_experience }}</p>
              </div>
              <div class="text-left">
                <span
                  v-if="card.highlight_1!==''"
                  class="tag md-font-13 xs-font-9 word-keep-all">{{ card.highlight_1 }}</span>
                <span
                  v-if="card.highlight_2!==''"
                  class="tag md-font-13 xs-font-9 word-keep-all">{{ card.highlight_2 }}</span>
                <span
                  v-if="card.highlight_3!==''"
                  class="tag md-font-13 xs-font-9 word-keep-all">{{ card.highlight_3 }}</span>
              </div>
            </v-flex>
          </v-layout>
        </div>
      </div>
    </div>
    <v-snackbar
      v-model="snackbar"
      :top="true"
      :color="color">
      {{ errorMessages }}
      <v-btn
        dark
        flat
        @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </v-content>
</template>

<script>
import qs from 'qs'
import boyIcon from '@/assets/img/boy_icon.jpg'
import girlIcon from '@/assets/img/girl_icon.jpg'
import boyTeacher from '@/assets/img/boy_teacher.jpg'
import girlTeacher from '@/assets/img/girl_teacher.jpg'
import prove from '@/assets/img/prove.png'
import cardBg1 from '@/assets/img/lesson_card_bg1.jpg'
import cardBg2 from '@/assets/img/lesson_card_bg2.jpg'
import cardBg3 from '@/assets/img/lesson_card_bg3.jpg'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
export default {
  name: 'Profile',
  metaInfo () {
    return {
      title: this.appName + '｜老師資訊'
    }
  },
  data: function () {
    return {
      boyTeacher,
      girlTeacher,
      boyIcon,
      girlIcon,
      prove,
      cardBg1,
      cardBg2,
      cardBg3,
      loading,
      loadingStatus: false,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      profileId: -1,
      profileApproved: false,
      profileAvgScore: 0,
      profileReviewedTimes: 0,
      profileEduApproved: false,
      profileEdu1: '',
      profileEdu2: '',
      profileEdu3: '',
      profileJobApproved: false,
      profileJob: '',
      profileExpApproved: false,
      profileExp: '',
      profileAllApproved: false,
      profileNickname: '',
      profileYear: '',
      profileIntro: '',
      profileTag: '',
      profileGender: null,
      profilePhoto: '',
      profileSelectTime: [],
      resLessonList: []
    }
  },
  computed: {
    ...mapState(['root', 'appName', 'dologinView', 'doChatView', 'doChatAction', 'doChatroomID']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  watch: {
    '$route.fullPath': function (newValue) {
      if (this.$route.params.id) {
        this.profileId = this.$route.params.id
      } else {
        this.profileId = -1
      }
      this.resLessonList = []
      this.getTeacherProfile()
      this.getLessonList()
    }
  },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  mounted () {
    let publicPage = true
    if (localStorage.getItem('token') || publicPage) {
      let body = {
        userId: this.userInfo ? this.userInfo.user_id : -1,
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              if (this.$route.params.id) {
                this.profileId = this.$route.params.id
              } else {
                this.profileId = -1
              }
              this.getTeacherProfile()
              this.getLessonList()
            } else {
              // this.$router.push('/')
            }
          } else {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else {
      // this.$router.push('/')
    }
  },
  methods: {
    test () {
      alert('未開放')
    },
    scrollTop () {
      scroll(0, 0)
    },
    getTeacherProfile () {
      this.$http.get(`/api/account/returnTeacherProfileForPublicViewing/?userID=${this.profileId}&type=teacher`).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.profileApproved = res.data.id_approved
            this.profileAvgScore = res.data.all_lesson_score_mean
            this.profileReviewedTimes = res.data.total_number_of_remark
            this.profileEdu1 = res.data.education_1
            this.profileEdu2 = res.data.education_2
            this.profileEdu3 = res.data.education_3
            this.profileEduApproved = res.data.education_approved
            this.profileJob = res.data.company
            this.profileJobApproved = res.data.work_approved
            this.profileExp = res.data.special_exp
            this.profileExpApproved = res.data.other_approved
            this.profileAllApproved = res.data.is_approved
            this.profileNickname = res.data.nickname
            this.profileYear = res.data.tutor_experience
            this.profileIntro = res.data.intro
            this.profileTag = res.data.subject_type
            this.profileGender = res.data.is_male ? boyIcon : girlIcon
            if (res.data.thumbnail_dir !== '') {
              this.profilePhoto = res.data.thumbnail_dir
            } else {
              if (res.data.is_male) {
                this.profilePhoto = this.boyTeacher
              } else {
                this.profilePhoto = this.girlTeacher
              }
            }
            this.profileSelectTime = res.data.general_available_time
          } else {
            this.$router.push('/')
          }
        })
    },
    getLessonList () {
      this.loadingStatus = true
      let body = {
        userID: this.userInfo ? this.userInfo.user_id : -1,
        qty: -1,
        keywords: '',
        only_show_ones_favorites: false,
        only_show_lessons_by_this_teacher_s_auth_id: this.profileId,
        ordered_by: '',
        filtered_by: ''
      }
      this.$http.post('/api/lesson/getLessonCardsForCommonUsers/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          this.loadingStatus = false
          if (res.status === 'success') {
            this.resLessonList = res.data
          } else {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          }
        })
    },
    doFavorite (id, status) {
      if (localStorage.getItem('token')) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          lessonID: id,
          status: status
        }
        this.$http.post('/api/lesson/addOrRemoveFavoriteLessons/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.getLessonList()
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已' + (status ? '加入' : '移除') + '收藏'
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    doConnectChatroom () {
      if (localStorage.getItem('token')) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          chatUserID: this.profileId
        }
        this.$http.post('/api/chat/checkOrCreateChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = '已開啟聊天室'
              this.$store.state.doChatView = true
              // this.$store.state.doChatAction = 'message'
              this.$store.state.doChatroomID = res.data.chatID
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    }
  }
}
</script>
