      let body = {
        userId: this.userInfo ? this.userInfo.user_id : -1,
        url: '/landing'
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              // do...
            } else {
              // this.$router.push('/')
            }
          } else {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else {
      // this.$router.push('/')
    }
  },
  methods: {
    test () {
      alert('XXXX')
    },
    scroll () {
      scroll(0, document.body.clientHeight)
    },
    userEmailFailMsg () {
      if (this.loginError && this.userEmail === '') {
        return '✖ 請輸入帳號(Email)'
      } else if (this.userEmail === '') {
        this.userEmailVerify = true
        // return '請輸入帳號(Email)'
        return ''
      } else {
        this.userEmailVerify = false
        // return '✔'
        return ''
      }
    },
    userPwdFailMsg () {
      if (this.loginError && this.userPwd === '') {
        return '✖ 請輸入密碼'
      } else if (this.userPwd === '') {
        this.userPwdVerify = true
        // return '請輸入密碼'
        return ''
      } else {
        this.userPwdVerify = false
        // return '✔'
        return ''
      }
    },
    userBirthFailMsg () {
      let isBirth = /^[0-9]{4}-[0-9]{2}-[0-9]{2}$/
      if (this.userBirth !== '' && !isBirth.test(this.userBirth)) {
        this.userBirthVerify = true
        return '✖ 生日輸入格式錯誤，格式為1900-01-01'
      } else if (this.userBirth !== '') {
        this.userBirthVerify = false
        return '✔'
      } else if (this.verifyError && this.userBirth === '') {
        this.userBirthVerify = true
        return '✖ 請輸入您的生日，格式為1900-01-01'
      } else {
        this.userBirthVerify = true
        return '請輸入您的生日，格式為1900-01-01'
      }
    },
    userMobileFailMsg () {
      let isMobile = /^09[0-9]{2}-[0-9]{6}$/
      if (this.verifyError && this.userMobile === '') {
        this.userMobileVerify = true
        return '✖ 請輸入您的手機號碼，格式為09XX-XXXXXX'
      } else if (this.userMobile === '') {
        this.userMobileVerify = true
        return '請輸入您的手機號碼，格式為09XX-XXXXXX'
      } else if (!isMobile.test(this.userMobile)) {
        this.userMobileVerify = true
        return '✖ 手機號碼格式錯誤，格式為09XX-XXXXXX'
      } else {
        this.userMobileVerify = false
        return '✔'
      }
    },
    userResetPwdFailMsg () {
      if (this.resetError && this.userResetPwd === '') {
        this.regResetPwdVerify = true
        return '✖ 請輸入密碼'
      } else if (this.userResetPwd === '') {
        this.userResetPwdVerify = true
        return '請輸入密碼'
      } else if (this.userResetPwd.length < 8) {
        this.regResetPwdVerify = true
        return '✖ 密碼請勿少於8個字'
      } else {
        this.userResetPwdVerify = false
        return '✔'
      }
    },
    userResetConfirmPwdFailMsg () {
      if (this.resetError && this.userResetConfirmPwd === '') {
        this.userResetConfirmPwdVerify = true
        return '✖ 請再次輸入密碼'
      } else if (this.userResetConfirmPwd === '') {
        this.userResetConfirmPwdVerify = true
        return '請再次輸入密碼'
      } else if (this.userResetConfirmPwd.length < 8) {
        this.userResetConfirmPwdVerify = true
        return '✖ 密碼請勿少於8個字'
      } else if (this.userResetPwd !== this.userResetConfirmPwd) {
        this.userResetConfirmPwdVerify = true
        return '✖ 密碼輸入不一致'
      } else {
        this.userResetConfirmPwdVerify = false
        return '✔'
      }
    },
    // loginT () {
    //   this.userEmail = 't00001@edony_test.com'
    //   this.userPwd = '        '
    //   this.login()
    // },
    // loginS () {
    //   this.userEmail = 's00010@edony_test.com'
    //   this.userPwd = '        '
    //   this.login()
    // },
    login () {
      if (this.userEmail !== '' && this.userPwd !== '') {
        let body = {
          userName: this.userEmail,
          userPwd: sha256(this.userPwd).toUpperCase()
        }
        this.$http.post('/api/account/signin/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              // this.loginStatus = true
              this.userInfo = {
                user_id: res.data.user_id,
                username: res.data.username,
                type: res.data.type,
                group: res.data.user_group,
                deposit: res.data.deposit,
                is_male: res.data.is_male,
                nickname: res.data.nickname,
                picture: res.data.picture + '?t=' + new Date().getTime()
              }
              if (res.data.picture === '' || res.data.picture === null) {
                if (res.data.type === 'teacher') {
                  if (res.data.is_male) {
                    this.userInfo.picture = boyTeacher
                  } else {
                    this.userInfo.picture = girlTeacher
                  }
                } else if (res.data.type === 'student') {
                  if (res.data.is_male) {
                    this.userInfo.picture = boyStudent
                  } else {
                    this.userInfo.picture = girlStudent
                  }
                }
              } else {
                this.$store.state.snapshot = this.userInfo.picture
              }
              localStorage.setItem('user', JSON.stringify(this.userInfo))
              this.loginView = false
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '登入成功'
              localStorage.setItem('token', res.data.user_token)
              localStorage.setItem('systemChatroomID', res.data.system_chatrooID)
              this.$store.state.loginStatus = true
              this.$router.push('/')
              // this.$router.go(0)
            } else {
              scroll(0, 0)
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.loginError = true
      }
    },
    logout () {
      const id = JSON.parse(localStorage.getItem('user')) ? JSON.parse(localStorage.getItem('user')).user_id : ''
      let body = {
        userId: id
      }
      this.$http.post('/api/account/logout/', qs.stringify(body))
      // .then(response => {
      // }
      localStorage.removeItem('token')
      localStorage.removeItem('user')
      this.$store.state.loginStatus = false
      delete this.$http.defaults.headers.common['Authorization']
      this.$router.go(0)
    },
    identity () {
      // 身份驗證
      if (this.userEmail !== '' && this.userBirthFailMsg().indexOf('✔') !== -1 && this.userMobileFailMsg().indexOf('✔') !== -1) {
        let body = {
          userName: this.userEmail,
          userBirth: this.userBirth,
          userMobile: this.userMobile
        }
        scroll(0, 0)
        this.$http.post('/api/account/memberForgotPassword/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '身份驗證成功'
              this.userID = res.data.userID
              localStorage.setItem('token', res.data.token)
              this.forgetPwdView = false
              this.resetForgetPwdView = true
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.verifyError = true
      }
    },
    doForgetResetPwd () {
      // 重置密碼
      if (this.userResetPwdFailMsg().indexOf('✔') !== -1 && this.userResetConfirmPwdFailMsg().indexOf('✔') !== -1) {
        let body = {
          userID: this.userID,
          newUserPwd: sha256(this.userResetPwd).toUpperCase()
        }
        this.$http.post('/api/account/memberResetPassword/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '重置密碼成功，請使用新密碼登入'
              localStorage.removeItem('token')
              this.resetForgetPwdView = false
              this.loginView = true
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.resetError = true
      }
    }
  }
}
</script>
