<template>
  <v-container
    id="timeselector"
    p-0
    m-0>
    <v-layout justify-center>
      <v-flex
        xs12>
        <div class="are-alert m-0 light-blue">
          <span class="are-alert-title">{{ title }}</span>
          <div
            v-if="selectWeekView"
            class="float-right week-menu-icon">
            <v-icon
              class="cursor"
              @click="doAgoWeeks()">mdi-menu-left</v-icon>
            <v-icon
              class="cursor"
              @click="doNextWeeks()">mdi-menu-right</v-icon>
          </div>
        </div>
        <h5
          v-if="selectWeekView"
          class="font-weight-bold text-center">
          {{ startDay }}-{{ endDay }}</h5>
        <div
          :class="{'week-menu-day': selectWeekView}"
          class="selector-time-week-title">
          <div
            :class="{ 'active': selectWeek === 0 }"
            class="cursor"
            @click="selectWeek=0">日<h4 v-if="selectWeekView">{{ weekDay[0] }}</h4></div>
          <div
            :class="{ 'active': selectWeek === 1 }"
            class="cursor"
            @click="selectWeek=1">一<h4 v-if="selectWeekView">{{ weekDay[1] }}</h4></div>
          <div
            :class="{ 'active': selectWeek === 2 }"
            class="cursor"
            @click="selectWeek=2">二<h4 v-if="selectWeekView">{{ weekDay[2] }}</h4></div>
          <div
            :class="{ 'active': selectWeek === 3 }"
            class="cursor"
            @click="selectWeek=3">三<h4 v-if="selectWeekView">{{ weekDay[3] }}</h4></div>
          <div
            :class="{ 'active': selectWeek === 4 }"
            class="cursor"
            @click="selectWeek=4">四<h4 v-if="selectWeekView">{{ weekDay[4] }}</h4></div>
          <div
            :class="{ 'active': selectWeek === 5 }"
            class="cursor"
            @click="selectWeek=5">五<h4 v-if="selectWeekView">{{ weekDay[5] }}</h4></div>
          <div
            :class="{ 'active': selectWeek === 6 }"
            class="cursor"
            @click="selectWeek=6">六<h4 v-if="selectWeekView">{{ weekDay[6] }}</h4></div>
        </div>
        <div class="selector-time-item-area">
          <div
            v-for="(time, t) in (selectDateDefault ? selectTimeList[arrayIndex] : selectTimeList[selectWeek])"
            :key="'time' + t"
            :class="{ 'cursor': (!readonly || (time.select && !time.booked && reservation)), 'active': ((!reservation || time.open) && time.select), 'booking':time.booked, 'open': ((!reservation || !time.open) && time.select), 'disabled': (readonly && !time.select) }"
            @click="doSelectTime(selectWeek, time.value, time.select, time.open)">{{ time.text }}</div>
        </div>
        <!-- <pre style="word-wrap: break-word; white-space: pre-wrap;">{{ msg }}</pre> -->
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'TimeSelector',
  props: {
    // resConfirm: {
    //   type: Boolean,
    //   default: true
    // },
    readonly: {
      type: Boolean,
      default: false
    },
    reservation: {
      type: Boolean,
      default: false
    },
    selectWeekView: {
      type: Boolean,
      default: false
    },
    selectTimeDefault: {
      type: String,
      default: null
    },
    selectDateDefault: {
      type: Array,
      default: null
    },
    selectDateBooked: {
      type: Array,
      default: null
    }
  },
  data: function () {
    return {
      title: this.readonly ? '每週可預約時段' : '時段選擇器',
      selectWeek: 0,
      selectTimeList: [],
      selectOpenTimeList: [],
      selectTimeDefaultList: [],
      selectDateDefaultList: [],
      selectDateBookedList: [],
      startDay: this.$moment(new Date(this.$moment(new Date()).format('YYYY/MM/DD') + ' 00:00:00:0000').getTime() - (86400000 * new Date().getDay())).format('YYYY/MM/DD'),
      endDay: this.$moment(new Date(this.$moment(new Date()).format('YYYY/MM/DD') + ' 00:00:00:0000').getTime() + (86400000 * (6 - new Date().getDay()))).format('YYYY/MM/DD'),
      weekDay: []
    }
  },
  computed: {
    selectTime () {
      let res = ''
      let week = [6, 0, 1, 2, 3, 4, 5]
      this.selectTimeList.forEach((w, index) => {
        let array = []
        w.forEach((t) => {
          if (t.select) {
            array.push(t.value)
          }
        })
        // if (array.length > 0) {
        //   res = res + (week[index] + ':' + array.join() + ';')
        // }
        res = res + (week[index] + ':' + array.join() + ';')
      })
      return res
    },
    selectOpenTime () {
      let res = []
      this.selectTimeList.forEach((d, index) => {
        let array = []
        d.forEach((t) => {
          if (t.open) {
            array.push(t.value)
          }
        })
        if (array.length > 0) {
          let day = this.$moment(new Date(this.$moment(new Date()).format('YYYY/MM/DD') + ' 00:00:00:0000').getTime() + (86400000 * index)).format('YYYY/MM/DD')
          res.push(day + ':' + array)
        }
      })
      return res
    },
    selectOpenTimeView () {
      let res = []
      this.selectTimeList.forEach((d, index) => {
        let arrayView = []
        d.forEach((t) => {
          if (t.open) {
            let text = (((t.value < 20) ? '0' : '') + parseInt(t.value / 2) + ':' + ((t.value % 2) ? '30' : '00') + '-' + (((t.value + 1) < 20) ? '0' : '') + parseInt((t.value + 1) / 2) + ':' + (((t.value + 1) % 2) ? '30' : '00'))
            arrayView.push({ text: text, code: t.value })
          }
        })
        if (arrayView.length > 0) {
          let day = this.$moment(new Date(this.$moment(new Date()).format('YYYY/MM/DD') + ' 00:00:00:0000').getTime() + (86400000 * index)).format('YYYY/MM/DD')
          res.push({ date: day, index: index, time: arrayView })
        }
      })
      return res
    },
    arrayIndex () {
      let res = ((new Date(this.startDay).getTime() + (86400000 * this.selectWeek) - new Date(this.$moment(new Date()).format('YYYY/MM/DD') + ' 00:00:00:0000').getTime()) / 86400000)
      return res
    }
  },
  watch: {
    selectWeek (val) {
      this.getDateTime(val)
    },
    startDay () {
      this.getDateTime(this.selectWeek)
    }
  },
  mounted () {
    if (this.selectWeekView) {
      this.selectWeek = Number(new Date().getDay())
      this.doWeekDay()
    }
    if (this.selectDateDefault) {
      this.selectDateDefault.forEach((d) => {
        let Day = d.split(':')
        let Time = Day[1].split(',')
        this.selectDateDefaultList.push({ date: Day[0], time: Time })
      })
      if (this.selectDateBooked) {
        this.selectDateBooked.forEach((d) => {
          let Day = d.split(':')
          let Time = Day[1].split(',')
          this.selectDateBookedList.push({ date: Day[0], time: Time })
        })
      }
    } else if (this.selectTimeDefault !== null && this.selectTimeDefault !== '') {
      for (let w = 0; w < 7; w++) {
        this.selectTimeDefaultList.push([])
      }
      let weekList = [6, 0, 1, 2, 3, 4, 5]
      let List = this.selectTimeDefault.split(';')
      List.forEach((w) => {
        if (w !== '') {
          let Week = w.split(':')
          let Time = Week[1].split(',')
          weekList.forEach((list, index) => {
            if (list === Number(Week[0])) {
              this.selectTimeDefaultList[index] = Time
            }
          })
        }
      })
    }
    this.selectTimeList = []
    if (this.selectDateDefault) {
      for (let d = 0; d < 180; d++) {
        this.selectTimeList.push([])
        for (let t = 0; t < 48; t++) {
          this.selectTimeList[d].push({ open: false, select: false, booked: false, value: t, text: (((t < 20) ? '0' : '') + parseInt(t / 2) + ':' + ((t % 2) ? '30' : '00') + '-' + (((t + 1) < 20) ? '0' : '') + parseInt((t + 1) / 2) + ':' + (((t + 1) % 2) ? '30' : '00')) })
        }
      }
      this.selectDateDefaultList.forEach((i, index) => {
        let days = (new Date(i.date + ' 00:00:00') - new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate(), 0, 0, 0)) / 86400000
        if (days >= 0) {
          for (let t = 0; t < 48; t++) {
            i.time.forEach((tt) => {
              if (t === Number(tt)) {
                this.selectTimeList[days][t].select = true
              }
            })
          }
        }
      })
      if (this.selectDateBooked) {
        this.selectDateBookedList.forEach((b, index) => {
          let days = (new Date(b.date + ' 00:00:00') - new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate(), 0, 0, 0)) / 86400000
          if (days >= 0) {
            for (let t = 0; t < 48; t++) {
              b.time.forEach((tt) => {
                if (t === Number(tt)) {
                  this.selectTimeList[days][t].booked = true
                }
              })
            }
          }
        })
      }
      // for (let d = 0; d < 180; d++) {
      //   let date = this.$moment(new Date().getTime() + (86400000 * d)).format('YYYY-MM-DD')
      //   let arrayIndex = 0
      //   let dateConform = false
      //   this.selectDateDefaultList.forEach((i, index) => {
      //     if (i.date === date) {
      //       arrayIndex = index
      //       dateConform = true
      //     }
      //   })
      //   this.selectTimeList.push([])
      //   for (let t = 0; t < 48; t++) {
      //     let select = false
      //     if (dateConform) {
      //       this.selectDateDefaultList[arrayIndex].time.forEach((tt) => {
      //         if (t === Number(tt)) {
      //           select = true
      //         }
      //       })
      //     }
      //     this.selectTimeList[d].push({ open: false, select: select, value: t, text: (((t < 20) ? '0' : '') + parseInt(t / 2) + ':' + ((t % 2) ? '30' : '00') + '-' + (((t + 1) < 20) ? '0' : '') + parseInt((t + 1) / 2) + ':' + (((t + 1) % 2) ? '30' : '00')) })
      //   }
      // }
    } else {
      for (let w = 0; w < 7; w++) {
        this.selectTimeList.push([])
        for (let t = 0; t < 48; t++) {
          let select = false
          if (this.selectTimeDefaultList.length > 0) {
            this.selectTimeDefaultList[w].forEach((s) => {
              if (t === Number(s) && s !== '') {
                select = true
              }
            })
          }
          this.selectTimeList[w].push({ open: false, select: select, booked: false, value: t, text: (((t < 20) ? '0' : '') + parseInt(t / 2) + ':' + ((t % 2) ? '30' : '00') + '-' + (((t + 1) < 20) ? '0' : '') + parseInt((t + 1) / 2) + ':' + (((t + 1) % 2) ? '30' : '00')) })
        }
      }
    }
  },
  methods: {
    doWeekDay () {
      this.weekDay = []
      for (let w = 0; w < 7; w++) {
        this.weekDay.push(this.$moment(new Date(this.startDay).getTime() + (86400000 * w)).format('DD'))
      }
    },
    doAgoWeeks () {
      if (this.$moment(new Date(this.startDay).getTime()) > this.$moment(new Date(this.$moment(new Date()).format('YYYY/MM/DD') + ' 00:00:00:0000').getTime())) {
        this.startDay = this.$moment(new Date(this.startDay).getTime() - (86400000 * 7)).format('YYYY/MM/DD')
        this.endDay = this.$moment(new Date(this.endDay).getTime() - (86400000 * 7)).format('YYYY/MM/DD')
        this.doWeekDay()
      }
    },
    doNextWeeks () {
      this.startDay = this.$moment(new Date(this.startDay).getTime() + (86400000 * 7)).format('YYYY/MM/DD')
      this.endDay = this.$moment(new Date(this.endDay).getTime() + (86400000 * 7)).format('YYYY/MM/DD')
      this.doWeekDay()
    },
    getDateTime (week) {
      // let date = this.$moment(new Date(this.startDay).getTime() + (86400000 * val)).format('YYYY-MM-DD')
      // this.selectTimeList[w].push({ select: select, value: t, text: (((t < 20) ? '0' : '') + parseInt(t / 2) + ':' + ((t % 2) ? '30' : '00') + '-' + (((t + 1) < 20) ? '0' : '') + parseInt((t + 1) / 2) + ':' + (((t + 1) % 2) ? '30' : '00')) })
      // weekDay[0]
    },
    doSelectTime (week, code, status, reservation) {
      if (!this.readonly && !this.reservation) {
        this.selectTimeList[week].forEach((t) => {
          if (t.value === code) {
            t.select = !status
          }
        })
        this.$emit('res-select-time', this.selectTime)
      } else if (this.reservation) {
        this.selectTimeList[this.arrayIndex].forEach((t) => {
          if (t.value === code) {
            t.open = !reservation
          }
        })
        this.$emit('res-select-open-time', this.selectOpenTime)

        // let date = this.$moment(new Date(this.startDay).getTime() + (86400000 * this.selectWeek)).format('YYYY-MM-DD')
        // let doing = 0
        // this.selectOpenTimeList.forEach((d, index) => {
        //   if (d.date === date) {
        //     d.time.forEach((t, i) => {
        //       if (t === code && reservation) {
        //         this.readyOutline.splice(c, 1)
        //       }
        //     })
        //     this.selectOpenTimeList[index].time.push()
        //     doing++
        //   }
        // })
        // this.selectOpenTimeList.push({ date: date, time:})
      }
    }
  }
}
</script>
