<template>
  <div
    id="blog-main"
    class="container my-0 p-0">
    <div v-html="resBlogData"/>
  </div>
</template>

<script>
// import $ from 'jquery'
import qs from 'qs'
import { mapState } from 'vuex'
export default {
  name: 'BlogMain',
  metaInfo () {
    return {
      title: this.appName + '｜Blog'
    }
  },
  data: () => ({
    // page: 1,
    resBlogData: ''
  }),
  computed: {
    ...mapState(['appName']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  watch: {
    '$route.fullPath': function (newValue) {
      this.doMounted()
    }
  },
  mounted () {
    this.doMounted()
  },
  methods: {
    doMounted () {
      let publicPage = true
      if (localStorage.getItem('token') || publicPage) {
        let body = {
          userId: this.userInfo ? this.userInfo.user_id : -1,
          url: this.$route.path
        }
        this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              if (res.data.authority) {
                // if (this.$route.params.page) {
                //   this.page = this.$route.params.page
                // }
                this.$http.get('/articles/').then(res => res.data)
                  .then(res => {
                    this.resBlogData = res
                  })
              } else {
                // this.$router.push('/')
              }
            } else {
              this.$store.dispatch('logout').then(() => {
                this.$router.go(0)
              })
            }
          })
      } else {
        // this.$router.push('/')
      }
    }
  }
}
</script>
