<template>
  <div
    id="blog-post"
    class="container my-0 p-0"
  >
    <!-- <div v-html="$xss(resBlogPostData)" /> -->
    <div v-html="resBlogPostData" />
  </div>
</template>

<script>
// import $ from 'jquery'
import qs from 'qs'
import { mapState } from 'vuex'
export default {
  name: 'BlogPost',
  metaInfo () {
    return {
      title: this.appName + '｜Blog文章'
    }
  },
  data: () => ({
    id: 0,
    resBlogPostData: ''
  }),
  computed: {
    ...mapState(['appName']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  watch: {
    '$route.fullPath': function (newValue) {
      this.doMounted()
    }
  },
  mounted () {
    this.doMounted()
  },
  methods: {
    doMounted () {
      let publicPage = true
      if (localStorage.getItem('token') || publicPage) {
        let body = {
          userId: this.userInfo ? this.userInfo.user_id : -1,
          url: this.$route.path
        }
        this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              if (res.data.authority) {
                if (this.$route.params.id) {
                  this.id = this.$route.params.id
                  this.$http.get(`/articles/article=${this.id}/`).then(res => res.data)
                    .then(res => {
                      this.resBlogPostData = res
                    })
                } else {
                  this.$router.push('/blog/main')
                }
              } else {
                // this.$router.push('/')
              }
            } else {
              this.$store.dispatch('logout').then(() => {
                this.$router.go(0)
              })
            }
          })
      } else {
        // this.$router.push('/')
      }
    }
  }
}
</script>
