    userEmailFailMsg () {
      if (this.loginError && this.userEmail === '') {
        return '✖ 請輸入帳號(Email)'
      } else if (this.userEmail === '') {
        this.userEmailVerify = true
        // return '請輸入帳號(Email)'
        return ''
      } else {
        this.userEmailVerify = false
        // return '✔'
        return ''
      }
    },
    userPwdFailMsg () {
      if (this.loginError && this.userPwd === '') {
        return '✖ 請輸入密碼'
      } else if (this.userPwd === '') {
        this.userPwdVerify = true
        // return '請輸入密碼'
        return ''
      } else {
        this.userPwdVerify = false
        // return '✔'
        return ''
      }
    },
    userBirthFailMsg () {
      let isBirth = /^[0-9]{4}-[0-9]{2}-[0-9]{2}$/
      if (this.userBirth !== '' && !isBirth.test(this.userBirth)) {
        this.userBirthVerify = true
        return '✖ 生日輸入格式錯誤，格式為1900-01-01'
      } else if (this.userBirth !== '') {
        this.userBirthVerify = false
        return '✔'
      } else if (this.verifyError && this.userBirth === '') {
        this.userBirthVerify = true
        return '✖ 請輸入您的生日，格式為1900-01-01'
      } else {
        this.userBirthVerify = true
        return '請輸入您的生日，格式為1900-01-01'
      }
    },
    userMobileFailMsg () {
      let isMobile = /^09[0-9]{2}-[0-9]{6}$/
      if (this.verifyError && this.userMobile === '') {
        this.userMobileVerify = true
        return '✖ 請輸入您的手機號碼，格式為09XX-XXXXXX'
      } else if (this.userMobile === '') {
        this.userMobileVerify = true
        return '請輸入您的手機號碼，格式為09XX-XXXXXX'
      } else if (!isMobile.test(this.userMobile)) {
        this.userMobileVerify = true
        return '✖ 手機號碼格式錯誤，格式為09XX-XXXXXX'
      } else {
        this.userMobileVerify = false
        return '✔'
      }
    },
    userResetPwdFailMsg () {
      if (this.resetError && this.userResetPwd === '') {
        this.regResetPwdVerify = true
        return '✖ 請輸入密碼'
      } else if (this.userResetPwd === '') {
        this.userResetPwdVerify = true
        return '請輸入密碼'
      } else if (this.userResetPwd.length < 8) {
        this.regResetPwdVerify = true
        return '✖ 密碼請勿少於8個字'
      } else {
        this.userResetPwdVerify = false
        return '✔'
      }
    },
    userResetConfirmPwdFailMsg () {
      if (this.resetError && this.userResetConfirmPwd === '') {
        this.userResetConfirmPwdVerify = true
        return '✖ 請再次輸入密碼'
      } else if (this.userResetConfirmPwd === '') {
        this.userResetConfirmPwdVerify = true
        return '請再次輸入密碼'
      } else if (this.userResetConfirmPwd.length < 8) {
        this.userResetConfirmPwdVerify = true
        return '✖ 密碼請勿少於8個字'
      } else if (this.userResetPwd !== this.userResetConfirmPwd) {
        this.userResetConfirmPwdVerify = true
        return '✖ 密碼輸入不一致'
      } else {
