    login () {
      if (this.userEmail !== '' && this.userPwd !== '') {
        let body = {
          userName: this.userEmail,
          userPwd: sha256(this.userPwd).toUpperCase()
        }
        this.$http.post('/api/account/signin/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.loginStatus = true
              this.userInfo = {
                user_id: res.data.user_id,
                username: res.data.username,
                type: res.data.type,
                group: res.data.user_group,
                deposit: res.data.deposit,
                is_male: res.data.is_male,
                nickname: res.data.nickname,
                picture: res.data.picture + '?t=' + new Date().getTime()
              }
              if (res.data.picture === '' || res.data.picture === null) {
                if (res.data.type === 'teacher') {
                  if (res.data.is_male) {
                    this.userInfo.picture = boyTeacher
                  } else {
                    this.userInfo.picture = girlTeacher
                  }
                } else if (res.data.type === 'student') {
                  if (res.data.is_male) {
                    this.userInfo.picture = boyStudent
                  } else {
                    this.userInfo.picture = girlStudent
                  }
                }
              } else {
                this.$store.state.snapshot = this.userInfo.picture
              }
              localStorage.setItem('user', JSON.stringify(this.userInfo))
              this.loginView = false
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '登入成功'
              localStorage.setItem('token', res.data.user_token)
              localStorage.setItem('systemChatroomID', res.data.system_chatrooID)
              this.$store.state.loginStatus = true
              // this.$router.push('/')
              this.$router.go(0)
            } else {
              scroll(0, 0)
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.loginError = true
      }
    },
