    logout () {
      const id = JSON.parse(localStorage.getItem('user')) ? JSON.parse(localStorage.getItem('user')).user_id : ''
      let body = {
        userId: id
      }
      this.$http.post('/api/account/logout/', qs.stringify(body))
      // .then(response => {
      // }
      localStorage.removeItem('token')
      localStorage.removeItem('user')
      this.$store.state.loginStatus = false
      delete this.$http.defaults.headers.common['Authorization']
      this.$router.go(0)
    },
    identity () {
      // 身份驗證
      if (this.userEmail !== '' && this.userBirthFailMsg().indexOf('✔') !== -1 && this.userMobileFailMsg().indexOf('✔') !== -1) {
        let body = {
          userName: this.userEmail,
          userBirth: this.userBirth,
          userMobile: this.userMobile
        }
        scroll(0, 0)
        this.$http.post('/api/account/memberForgotPassword/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '身份驗證成功'
              this.userID = res.data.userID
              localStorage.setItem('token', res.data.token)
              this.forgetPwdView = false
              this.resetForgetPwdView = true
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.verifyError = true
      }
    },
    doForgetResetPwd () {
      // 重置密碼
      if (this.userResetPwdFailMsg().indexOf('✔') !== -1 && this.userResetConfirmPwdFailMsg().indexOf('✔') !== -1) {
        let body = {
          userID: this.userID,
          newUserPwd: sha256(this.userResetPwd).toUpperCase()
        }
        this.$http.post('/api/account/memberResetPassword/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '重置密碼成功，請使用新密碼登入'
              localStorage.removeItem('token')
              this.resetForgetPwdView = false
              this.loginView = true
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.resetError = true
      }
    },
    doResetPwd () {
      // 修改密碼
      if (this.userOldPwdFailMsg().indexOf('✔') !== -1 && this.userNewPwdFailMsg().indexOf('✔') !== -1 && this.userNewConfirmPwdFailMsg().indexOf('✔') !== -1) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          oldUserPwd: sha256(this.userOldPwd).toUpperCase(),
          newUserPwd: sha256(this.userNewPwd).toUpperCase()
        }
        scroll(0, 0)
        this.$http.post('/api/lesson/getLessonCardsForCommonUsers/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '修改密碼成功，下次登入請記得改用新密碼'
              this.$store.state.doResetPwdView = false
              this.resetPwdView = false
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.modifyError = true
      }
    }
