<template>
  <div id="chatroom">
    <div v-if="doChatAction==='list'">
      <div class="topbox row">
        <span class="col-12 text-center">Message</span>
      </div>
      <div class="middlebox">
        <div
          v-if="updateStatus"
          class="loading"/>
        <div class="list-area">
          <div
            v-for="(room, r) in chatroom"
            :key="'r'+r"
            :class="{ 'new': room.chatUnreadMessageQty>0 }"
            class="list cursor-no-hover"
            @click="chatroomIndex=r;chatroomChatroomID=room.chatroomID;inChatroom(r);$store.state.doChatAction='message'">
            <div
              :style="'background-image:url('+((room.chatUserPath !== '') ? (root + room.chatUserPath) : (room.chatUsergender?(room.chatUserType==='teacher'?boyTeacher:boyStudent):(room.chatUserType==='teacher'?girlTeacher:girlStudent)))+');'"
              class="photo"/>
            <span class="name">{{ room.chatUserName }}</span>
            <span class="time">{{ (room.messageInfo.length>0)?($moment(room.messageInfo[(room.messageInfo.length-1)].messageCreateTime).format('HH:mm')):'' }}</span>
            <p>{{ (room.messageInfo.length>0)?(room.messageInfo[(room.messageInfo.length-1)].messageText):'' }}</p>
          </div>
        </div>
      </div>
    </div>
    <div v-if="doChatAction==='message'">
      <div class="topbox row">
        <v-icon
          class="col-1 cursor-no-hover"
          @click="outChatroom(chatroomIndex);$store.state.doChatAction='list'">mdi-chevron-left</v-icon>
        <span class="col-10 text-center">
          <div
            :style="'background-image:url('+((chatroom[chatroomIndex].chatUserPath !== '') ? (root + chatroom[chatroomIndex].chatUserPath) : (chatroom[chatroomIndex].chatUsergender?(chatroom[chatroomIndex].chatUserType==='teacher'?boyTeacher:boyStudent):(chatroom[chatroomIndex].chatUserType==='teacher'?girlTeacher:girlStudent)))+');'"
            class="photo"/>{{ chatroom[chatroomIndex].chatUserName }}
        </span>
        <!-- <v-icon
          v-if="userInfo.type!=='teacher'"
          class="col-1 text-white cursor-no-hover"
          @click="$store.state.doChatAction='info'">mdi-alert-circle-outline</v-icon> -->
        <v-icon
          v-if="userInfo.type!=='teacher'"
          class="col-1 text-white cursor-no-hover"
          @click="$router.history.current.path===('/account/profile/'+(chatroom[chatroomIndex].chatUserID||''))?'':$router.push('/account/profile/'+(chatroom[chatroomIndex].chatUserID||''))">mdi-alert-circle-outline</v-icon>
        <v-icon
          v-if="userInfo.type==='teacher'"
          class="col-1 text-white cursor-no-hover"/>
      </div>
      <div class="booking cursor-no-hover">
        <v-icon class="text-white left">mdi-chevron-double-right</v-icon>立即預約<v-icon class="text-white right">mdi-chevron-double-left</v-icon>
      </div>
      <div
        id="messagebox"
        :class="{'bg-booking':(!loadingStatus&&chatroom[chatroomIndex].chatUserLessonList!==[]&&userInfo.type!=='teacher')}"
        class="middlebox bg-message">
        <div
          v-for="(message, m) in chatroom[chatroomIndex].messageInfo"
          :key="'m'+m"
          :class="{'our': (message.messageType!=='1')&&(message.senderID===userInfo.user_id), 'other': (message.messageType!=='1')&&(message.senderID!==userInfo.user_id), 'system':message.messageType==='1', 'booked':message.messageType==='2'}"
          class="message">
          <span class="time">{{ $moment(message.messageCreateTime).format('HH:mm') }}</span>
          <p v-if="message.messageType==='0'">{{ message.messageText }}</p>
          <p v-if="message.messageType==='1'"><span>{{ $moment(message.messageCreateTime).format('HH:mm') }}</span><br>{{ message.messageText }}</p>
          <div v-if="message.messageType==='2'">
            <p class="subtitle"><v-icon class="text-white mr-1">mdi-clock-alert-outline</v-icon>預約資訊</p>
            <p class="title">{{ JSON.parse(message.messageText).lesson_title }}</p>
            <p><span>預約日期</span>{{ $moment(JSON.parse(message.messageText).bookingDate).format('YYYY年MM月DD日') }}</p>
            <p><span>預約時段</span>{{ JSON.parse(message.messageText).bookingTime }}</p>
            <v-btn
              dark
              flat
              class="m-0 mt-2 w-100 border border-white bg-select-color"
              @click="$router.push((userInfo.type==='teacher')?'/account/reservation':'/account/history')">
              {{ '前往' + (userInfo.type==='teacher')?'預約管理':'學習歷程' + '確認' }}
            </v-btn>
          </div>
          <span class="time">{{ $moment(message.messageCreateTime).format('HH:mm') }}</span>
        </div>
      </div>
      <div class="bottombox">
        <input
          v-model="messageText"
          placeholder="在此輸入訊息"
          @keydown.enter="websocketsend()">
        <v-icon
          class="text-white mr-1 cursor"
          @click="websocketsend()">mdi-send</v-icon>
      </div>
    </div>
    <div v-if="doChatAction==='info'">
      <div class="topbox row">
        <v-icon
          class="col-1 cursor-no-hover"
          @click="$store.state.doChatAction='message'">mdi-chevron-left</v-icon>
        <span
          v-if="doChatAction==='info'"
          class="col-10 text-center">教師資訊</span>
        <div class="col-1"/>
      </div>
      <div class="middlebox">
        <div class="profile">
          <div
            :style="'background-image:url('+((chatroom[chatroomIndex].chatUserPath !== '') ? (root + chatroom[chatroomIndex].chatUserPath) : (chatroom[chatroomIndex].chatUsergender?(chatroom[chatroomIndex].chatUserType==='teacher'?boyTeacher:boyStudent):(chatroom[chatroomIndex].chatUserType==='teacher'?girlTeacher:girlStudent)))+');'"
            class="photo"/>
          <p class="h4 subtitle mb-2">關於老師</p>
          <p class="content">{{ chatroom[chatroomIndex].chatUserIntro }}</p>
          <p class="h4 subtitle mb-2">開設課程</p>
          <div
            :class="{'loading':loadingStatus&&!chatroom[chatroomIndex].chatUserLessonList}"
            class="lesson">
            <p
              v-if="!loadingStatus&&chatroom[chatroomIndex].chatUserLessonList===[]"
              class="text-center w-100">尚未開設課程</p>
            <v-btn
              v-for="(lesson, l) in chatroom[chatroomIndex].chatUserLessonList"
              :key="'l'+l"
              dark
              flat
              @click="$router.push('/lesson/main/view/'+lesson.value)"
            >
              {{ lesson.text }}
            </v-btn>
          </div>
          <v-btn
            v-if="chatroom[chatroomIndex].chatUserLessonList&&chatroom[chatroomIndex].chatUserLessonList!==[]"
            dark
            flat
          >
            立即預約
          </v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import qs from 'qs'
import boyTeacher from '@/assets/img/boy_teacher.jpg'
import girlTeacher from '@/assets/img/girl_teacher.jpg'
import boyStudent from '@/assets/img/boy_student.jpg'
import girlStudent from '@/assets/img/girl_student.jpg'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
export default {
  name: 'Chatroom',
  // props: {
  //   readonly: {
  //     type: Boolean,
  //     default: false
  //   },
  //   selectTimeDefault: {
  //     type: String,
  //     default: null
  //   },
  //   selectDateDefault: {
  //     type: Array,
  //     default: null
  //   }
  // },
  data: () => ({
    boyTeacher,
    girlTeacher,
    boyStudent,
    girlStudent,
    loading,
    loadingStatus: false,
    updateStatus: false,
    websock: [],
    websockSystem: [],
    chatroom: [],
    chatroomIndex: -1,
    chatroomChatroomID: 0,
    messageText: ''
  }),
  // created () {
  //   this.initWebSocket()
  // },
  // destroyed () {
  //   this.websock.close()
  // },
  computed: {
    ...mapState(['root', 'loginStatus', 'doChatView', 'doChatAction', 'doChatroomID', 'doReadStatus']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    },
    chatUnreadMessageQty () {
      let qty = 0
      this.chatroom.forEach((room, index) => {
        qty = qty + room.chatUnreadMessageQty
      })
      return qty
    }
  },
  watch: {
    loginStatus (val) {
      // if (val) {
      //   this.getHistoricalMessage()
      //   this.initWebSocket()
      // } else {
      //   alert('已手動斷線')
      //   this.websock.close()
      //   this.$router.go(0)
      // }
    },
    doChatView (val) {
      if (val) {
        this.doOpenChatWindows()
      } else {
        this.doCloseChatWindows()
      }
    },
    doChatAction (val) {
      if (val === 'list') {
        this.chatroomIndex = -1
      }
    },
    doChatroomID (id) {
      if (id >= 0) {
        let open = false
        this.chatroom.forEach((room, index) => {
          if (room.chatroomID === id) {
            this.chatroomIndex = index
            this.$store.state.doChatAction = 'message'
            this.$emit('snackbar', ['green', '已進入與使用者' + this.chatroom[index].chatUserID + '的' + this.chatroom[index].chatroomID + '號聊天室'])
            open = true
            this.updateStatus = false
            this.$store.state.doChatroomID = -1
          }
        })
        if (!open) {
          this.doCreateChatroom(id)
        }
      }
    },
    chatUnreadMessageQty (val) {
      this.$store.state.doReadStatus = (val > 0)
    },
    chatroomIndex (val) {
      if (val >= 0) {
        this.chatroom[val].chatUnreadMessageQty = 0
      }
      this.infoIntro = ''
      this.infoLessonList = []
    }
  },
  mounted () {
    if (localStorage.getItem('token')) {
      this.websock = []
      this.websockSystem = []
      this.initSystemWebSocket()
      this.getHistoricalMessage()
      // if (this.userInfo.type === 'teacher') {
      //   setInterval(() => {
      //     // this.chatroom.forEach((room, index) => {
      //     //   this.websock[index].close()
      //     // })
      //     this.doNewChatroomConnect('老師')
      //   }, 1000 * 60 * 5)
      // }
    } else {
      this.chatroom.forEach((room, index) => {
        this.websock[index].close()
      })
      this.$emit('snackbar', ['red', '已手動斷線'])
    }
  },
  methods: {
    getTeacherProfile (id) {
      this.chatroom.forEach((room, index) => {
        this.$emit('snackbar', ['blue', '準備取得老師(ID:' + room.chatUserID + ')資料' + (id || '')])
        if (!id || (id === room.chatroomID)) {
          this.$emit('snackbar', ['blue', '取得老師(ID:' + room.chatUserID + ')資料中' + (id || '')])
          this.$http.get(`/api/account/returnTeacherProfileForPublicViewing/?userID=${room.chatUserID}&type=teacher`).then(res => res.data)
            .then(res => {
              if (res.status === 'success') {
                this.$emit('snackbar', ['green', '已取得老師(ID:' + room.chatUserID + ')資料' + (id || '')])
                this.chatroom[index].chatUserIntro = res.data.intro
              }
            })
        }
      })
    },
    getLessonList (id) {
      this.loadingStatus = true
      this.chatroom.forEach((room, index) => {
        this.$emit('snackbar', ['blue', '準備取得老師(ID:' + room.chatUserID + ')課程清單' + (id || '')])
        if (!id || (id === room.chatroomID)) {
          this.$emit('snackbar', ['blue', '取得老師(ID:' + room.chatUserID + ')課程清單中' + (id || '')])
          this.infoLessonList = []
          let body = {
            userID: this.userInfo ? this.userInfo.user_id : -1,
            qty: -1,
            keywords: '',
            only_show_ones_favorites: false,
            only_show_lessons_by_this_teacher_s_auth_id: room.chatUserID,
            ordered_by: '',
            filtered_by: ''
          }
          this.$http.post('/api/lesson/getLessonCardsForCommonUsers/', qs.stringify(body)).then(res => res.data)
            .then(res => {
              this.loadingStatus = false
              if (res.status === 'success') {
                this.$emit('snackbar', ['green', '已取得老師(ID:' + room.chatUserID + ')課程清單' + (id || '')])
                this.chatroom[index].chatUserLessonList = []
                res.data.forEach((lesson) => {
                  this.chatroom[index].chatUserLessonList.push({ value: lesson.lessonID, text: lesson.lesson_title })
                })
              }
            })
        }
      })
    },
    doNewChatroomConnect (type) {
      this.$emit('snackbar', ['blue', '準備取得歷史訊息(' + type + '端)'])
      if (localStorage.getItem('token')) {
        let chatroomTemp = []
        let chatroomList = []
        this.chatroom.forEach((room, index) => {
          chatroomList.push(room.chatroomID)
        })
        this.$emit('snackbar', ['blue', '取得歷史訊息中(' + type + '端)'])
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          user_type: this.userInfo.type
        }
        this.$http.post('/api/chat/contentAndHistoryOfChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.$emit('snackbar', ['green', '已取得歷史訊息(' + type + '端)'])
              chatroomTemp = res.data
              chatroomTemp.forEach((room, index) => {
                if (chatroomList.indexOf(room.chatroomID) === -1) {
                  this.chatroom.push(chatroomTemp[index])
                  this.$emit('snackbar', ['blue', '準備與' + room.chatroomID + '號聊天室連線(' + type + '端)'])
                  const wsuri = this.$ws + `/ws/chat/${this.userInfo ? this.userInfo.user_id : -1}_chatroom_${chatroomTemp[index].chatroomID}_0/`
                  this.websock.push(new WebSocket(wsuri))
                  this.websock[this.websock.length - 1].onmessage = this.websocketonmessage
                  this.websock[this.websock.length - 1].onopen = this.websocketonopen(chatroomTemp[index].chatUserID, chatroomTemp[index].chatroomID)
                  this.websock[this.websock.length - 1].onerror = this.websocketonerror
                  this.websock[this.websock.length - 1].onclose = this.websocketclose
                  if (!(this.userInfo.type === 'teacher' && this.$store.state.doChatAction !== 'list')) {
                    this.$store.state.doChatroomID = room.chatroomID
                    this.chatroomChatroomID = room.chatroomID
                  } else {
                    this.updateStatus = false
                  }
                }
              })
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    getHistoricalMessage (id) {
      this.$emit('snackbar', ['blue', '準備取得歷史訊息'])
      if (localStorage.getItem('token')) {
        this.$emit('snackbar', ['blue', '取得歷史訊息中'])
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          user_type: this.userInfo.type
        }
        this.$http.post('/api/chat/contentAndHistoryOfChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.$emit('snackbar', ['green', '已取得歷史訊息'])
              this.chatroom = res.data
              this.initWebSocket(id)
              if (this.userInfo.type === 'student') {
                // this.getTeacherProfile(id)
                // this.getLessonList(id)
              }
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    initSystemWebSocket () {
      // this.websockSystem = []
      const wsuri = this.$ws + `/ws/chat/${this.userInfo ? this.userInfo.user_id : -1}_chatroom_${localStorage.getItem('systemChatroomID')}_1/`
      this.websockSystem = new WebSocket(wsuri)
      this.websockSystem.onmessage = this.websocketSystemOnmessage
      this.websockSystem.onopen = this.websocketSystemOnopen('system', localStorage.getItem('systemChatroomID'))
      this.websockSystem.onerror = this.websocketSystemOnerror
      this.websockSystem.onclose = this.websocketSystemClose
      // const wsuri = this.$ws + `/ws/chat/1_chatroom_1/`
      // this.websock = new WebSocket(wsuri)
      // this.websock.onmessage = this.websocketonmessage
      // this.websock.onopen = this.websocketonopen
      // this.websock.onerror = this.websocketonerror
      // this.websock.onclose = this.websocketclose
      // console.log(this.websock)
    },
    initWebSocket (id) {
      // this.websock = []
      this.chatroom.forEach((room, index) => {
        if (!id || (id === room.chatroomID)) {
          const wsuri = this.$ws + `/ws/chat/${this.userInfo ? this.userInfo.user_id : -1}_chatroom_${room.chatroomID}_0/`
          this.websock.push(new WebSocket(wsuri))
          this.websock[index].onmessage = this.websocketonmessage
          this.websock[index].onopen = this.websocketonopen(room.chatUserID, room.chatroomID)
          this.websock[index].onerror = this.websocketonerror
          this.websock[index].onclose = this.websocketclose
        }
      })
      // const wsuri = this.$ws + `/ws/chat/1_chatroom_1/`
      // this.websock = new WebSocket(wsuri)
      // this.websock.onmessage = this.websocketonmessage
      // this.websock.onopen = this.websocketonopen
      // this.websock.onerror = this.websocketonerror
      // this.websock.onclose = this.websocketclose
      // console.log(this.websock)
    },
    websocketonerror () { // 连接建立失败重连
      // this.initWebSocket()
    },
    websocketSystemOnerror () { // 连接建立失败重连
      // this.initWebSocket()
    },
    websocketonopen (chatUserID, chatroomID) { // 连接建立之后执行send方法发送数据
      this.$emit('snackbar', ['green', '與使用者' + chatUserID + '的' + chatroomID + '號聊天室已連線'])
    },
    websocketSystemOnopen (chatUserID, chatroomID) { // 连接建立之后执行send方法发送数据
      this.$emit('snackbar', ['darkmagenta', '與使用者' + chatUserID + '的' + chatroomID + '號聊天室已連線'])
    },
    websocketonmessage (e) { // 数据接收
      let redata = JSON.parse(e.data)
      let update = true
      this.chatroom.forEach((room, index) => {
        if (redata.chatroomID === room.chatroomID) {
          update = false
          this.chatroom[index].messageInfo.push(redata)
          // scroll(document.getElementById('messagebox').scrollHeight, 400)
          // alert(document.getElementById('messagebox').scrollHeight)
          this.$emit('snackbar', ['blue', '已接收訊息:' + e.data])
          let roomID = this.chatroom[this.chatroomIndex] ? this.chatroom[this.chatroomIndex].chatroomID : -1
          if (roomID === redata.chatroomID && this.doChatView) {
            setTimeout(() => {
              var element = document.getElementById('messagebox')
              element.scrollTop = document.getElementById('messagebox').scrollHeight
            }, 100)
          } else {
            this.chatroom[index].chatUnreadMessageQty = this.chatroom[index].chatUnreadMessageQty + 1
          }
        }
      })
      if (update) {
        this.doUpdateChatroomList(redata.chatroomID)
      }
    },
    websocketSystemOnmessage (e) { // 数据接收
      let redata = JSON.parse(e.data)
      this.$emit('snackbar', ['darkmagenta', '(system)已接收訊息:' + e.data])
      if (redata.systemCode === 1) {
        this.updateStatus = true
        this.$emit('snackbar', ['darkmagenta', '(system)準備建立新聊天室'])
        this.doNewChatroomConnect('老師', redata.messageText.BtnCode)
        this.$store.state.doChatView = true
        // this.$store.state.doChatView = true
        // this.$store.state.doChatAction = 'list'
        // this.$store.state.doChatroomID = redata.messageText.BtnCode
      }
    },
    websocketsend () { // 数据发送
      if (this.messageText.length > 0) {
        let msg = {
          senderID: this.userInfo ? this.userInfo.user_id : -1,
          chatroomID: this.chatroomChatroomID,
          messageType: '0',
          messageText: this.messageText
        }
        // let msgSystem = {
        //   userID: this.userInfo ? this.userInfo.user_id : -1,
        //   chatID: localStorage.getItem('systemChatroomID'),
        //   messageType: '0',
        //   messageText: this.messageText
        // }
        this.websock[this.chatroomIndex].send(JSON.stringify(msg))
        // this.websockSystem.send(JSON.stringify(msgSystem))
        this.messageText = ''
        this.$emit('snackbar', ['green', '已發送訊息:' + JSON.stringify(msg)])
        // this.$emit('snackbar', ['darkmagenta', '(system)已發送訊息:' + JSON.stringify(msgSystem)])
      }
    },
    websocketclose () {
      this.$emit('snackbar', ['red', '已自動斷線，將自動重新連線'])
      this.initWebSocket()
    },
    websocketSystemClose () {
      this.$emit('snackbar', ['darkmagenta', 'System已自動斷線，將自動重新連線'])
      this.initSystemWebSocket()
    },
    doCreateChatroom (id) {
      this.updateStatus = true
      this.$store.state.doChatroomID = -1
      this.$emit('snackbar', ['blue', '已建立新聊天室，準備更新歷史訊息'])
      // this.getHistoricalMessage(id)
      this.doNewChatroomConnect('學生', id)
      // this.$store.state.doChatroomID = id
    },
    doUpdateChatroomList (id) {
      this.updateStatus = true
      this.$emit('snackbar', ['blue', '接收到新聊天室的訊息，準備更新歷史訊息'])
      this.doNewChatroomConnect('學生', id)
    },
    doOpenChatWindows () {
      this.$emit('snackbar', ['green', '已開啟' + this.$store.state.doChatAction + '聊天室區域'])
    },
    doCloseChatWindows () {
      this.$emit('snackbar', ['black', '已從' + this.$store.state.doChatAction + '關閉聊天室區域'])
    },
    inChatroom (n) {
      this.$emit('snackbar', ['green', '已進入與使用者' + this.chatroom[n].chatUserID + '的' + this.chatroom[n].chatroomID + '號聊天室'])
    },
    outChatroom (n) {
      this.$emit('snackbar', ['black', '已離開與使用者' + this.chatroom[n].chatUserID + '的' + this.chatroom[n].chatroomID + '號聊天室'])
    }
  }
}
</script>
<style lang='scss'>
$main-color: #2ac5ff;
$select-color: #3db1c3;
$label-color: #7a7a7a;
$not-select-color: #a7a7a7;
$button-color: #fdbe5d;
$chat-color: #fdbe5d;
$message-color: #fdd620;

#chatroom{
  width: 500px;
  height: 568px;
  position: fixed;
  right: 20px;
  bottom: 100px;
  z-index: 999;
  border-radius: 30px 30px 0 30px;
  box-shadow: 2px 1px 10px #707070;
  .topbox{
    width: 100%;
    height: 80px;
    background-color: $chat-color;
    padding: 0 15px;
    margin-right: 0 !important;
