<script>
import qs from 'qs'
import boyTeacher from '@/assets/img/boy_teacher.jpg'
import girlTeacher from '@/assets/img/girl_teacher.jpg'
import prove from '@/assets/img/prove.png'
import cardBg1 from '@/assets/img/lesson_card_bg1.jpg'
import cardBg2 from '@/assets/img/lesson_card_bg2.jpg'
import cardBg3 from '@/assets/img/lesson_card_bg3.jpg'
import beTeacher from '@/assets/img/be_teacher.png'
import searchLesson from '@/assets/img/search_lesson.png'
import loading from '@/assets/img/loading.gif'
import { mapState } from 'vuex'
export default {
  name: 'Home',
  metaInfo () {
    return {
      title: this.appName + '｜首頁'
    }
  },
  data: function () {
    return {
      boyTeacher,
      girlTeacher,
      prove,
      cardBg1,
      cardBg2,
      cardBg3,
      beTeacher,
      searchLesson,
      loading,
      loadingStatus: false,
      loadingView: false,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      homebannerbarSearch: '',
      // recommendedCoursesSortList: false,
      homeBannerBarSort: 0,
      homeBannerBarQty: 3,
      resBannerBarPC: [],
      resBannerBarMobile: [],
      resLessonRecommendList: []
    }
  },
  computed: {
    ...mapState(['root', 'appName', 'dologinView']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    },
    isTeacherCB () {
      if (this.userInfo) {
        if (this.userInfo.group.indexOf('test_teacher') > -1) {
          return true
        } else {
          return false
        }
      } else {
        return false
      }
    },
    isStudentCB () {
      if (this.userInfo) {
        if (this.userInfo.group.indexOf('test_student') > -1) {
          return true
        } else {
          return false
        }
      } else {
        return false
      }
    }
  },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  mounted () {
    if (!(this.isTeacherCB || this.isStudentCB)) {
      if (localStorage.getItem('token')) {
        if ((this.userInfo ? this.userInfo.type : '') === 'teacher') {
          this.$router.push('/account/lesson')
        } else if ((this.userInfo ? this.userInfo.type : '') === 'student') {
          this.$router.push('/account/info/student')
        }
      } else {
        this.$router.push('/landing')
      }
    } else {
      let publicPage = true
      this.loadingView = true
      if (localStorage.getItem('token') || publicPage) {
        let body = {
          userId: this.userInfo ? this.userInfo.user_id : -1,
          url: '/home'
        }
        this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              if (res.data.authority) {
                this.$http.get('/api/getBannerBar/').then(res => res.data)
                  .then(res => {
                    this.resBannerBarPC = []
                    this.resBannerBarMobile = []
                    res.data.forEach((b) => {
                      if (b.type === 'pc') {
                        this.resBannerBarPC.push(b)
                      } else if (b.type === 'mobile') {
                        this.resBannerBarMobile.push(b)
                      }
                    })
                  })
                this.getLessonRecommend()
                this.timer = setInterval(() => {
                  this.homebannerbar(1)
                }, 5000)
              } else {
                // this.$router.push('/')
              }
            } else {
              this.$store.dispatch('logout').then(() => {
                this.$router.go(0)
              })
            }
          })
      } else {
        // this.$router.push('/')
      }
    }
  },
  methods: {
    test () {
      alert('未開放')
    },
    homebannerbar (val) {
      this.homeBannerBarSort = this.homeBannerBarSort + val
      if (this.homeBannerBarSort > this.resBannerBarPC.length - 1) {
        this.homeBannerBarSort = 0
      }
      if (this.homeBannerBarSort < 0) {
        this.homeBannerBarSort = this.resBannerBarPC.length - 1
      }
    },
    homebannerbarbtn (val) {
      this.homeBannerBarSort = val
    },
    getLessonRecommend () {
      this.loadingStatus = true
      let body = {
        userID: this.userInfo ? this.userInfo.user_id : -1,
        qty: 4,
        keywords: '',
        only_show_ones_favorites: false,
        only_show_lessons_by_this_teacher_s_auth_id: -1,
        ordered_by: '',
        filtered_by: ''
      }
      this.$http.post('/api/lesson/getLessonCardsForCommonUsers/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          this.loadingStatus = false
          if (res.status === 'success') {
            this.resLessonRecommendList = res.data
          } else {
            this.snackbar = true
            this.color = 'red'
            this.errorMessages = res.errMsg
          }
        })
    },
    doFavorite (id, status) {
      if (localStorage.getItem('token')) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          lessonID: id,
          status: status
        }
        this.$http.post('/api/lesson/addOrRemoveFavoriteLessons/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.getLessonRecommend()
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已' + (status ? '加入' : '移除') + '收藏'
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    }
  }
}
</script>
