// https://vuex.vuejs.org/en/mutations.html
import { set } from '@/utils/vuex'

export default {
  setloginView: set('login'),

  auth_request (state) {
    state.authStatus = 'loading'
  },
  auth_success (state, { token, user, normalMenu, devMenu, moduleDataInitDate }) {
    state.authStatus = 'success'
    state.token = token
    state.user = user
    state.normalMenu = normalMenu
    state.devMenu = devMenu
    state.moduleDataInitDate = moduleDataInitDate
  },
  auth_error (state) {
    state.authStatus = 'error'
  },
  logout (state) {
    state.authStatus = ''
    state.token = ''
  },
  setTableList (state, tableList) {
    state.tableList = tableList
  }
}
