<script>
import $ from 'jquery'
import qs from 'qs'
import sha256 from 'sha256'
import { mapState } from 'vuex'
export default {
  name: 'StudentRegister',
  metaInfo () {
    return {
      title: this.appName + '｜學生註冊'
    }
  },
  data: function () {
    return {
      snackbar: false,
      color: 'red',
      errorMessages: '',
      regEmail: '',
      regEmailVerify: false,
      regPwd: '',
      regPwdVerify: false,
      regPwdConfirm: '',
      regPwdConfirmVerify: false,
      regMobile: '',
      regMobileVerify: false,
      regName: '',
      regNameVerify: false,
      regNickname: '',
      regNicknameVerify: false,
      regRole: '',
      regRoleVerify: false,
      regGender: '',
      regGenderVerify: false,
      // regBirth: '',
      regBirth: this.$moment(new Date('2000-01-01')).format('YYYY-MM-DD'),
      regBirthVerify: false,
      regBirthMenu: false,
      regHeadshot: [
        {
          file: '',
          name: '',
          url: ''
        }
      ],
      regNotifiemail: '',
      regNotifiemailVerify: false,
      userAgreementOpen: false,
      userAgreementCheck: false,
      submitVerify: false,
      submit: false
    }
  },
  computed: {
    ...mapState(['appName']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  mounted () {
    let publicPage = true
    if (!localStorage.getItem('token') && publicPage) {
      let body = {
        userId: this.userInfo ? this.userInfo.user_id : -1,
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              // do...
            } else {
              // this.$router.push('/')
            }
          } else {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else if (localStorage.getItem('token')) {
      this.$router.push('/')
    } else {
      // this.$router.push('/')
    }
  },
  methods: {
    setHeadshot (e) {
      let files = e.target.files || e.dataTransfer.files
      if (e.srcElement.files.item(0).size > (4 * 1024 * 1024)) {
        $('#headshot-file').val('')
        alert('上傳照片不得超過4MB')
      } else {
        // alert('Uploading: ' + e.srcElement.files.item(0).name + ' @ ' + e.srcElement.files.item(0).size + 'bytes')
        // console.log(window.URL.createObjectURL(e.srcElement.files.item(0)))
        // console.log(e.srcElement.files.item(0).name)
        // let target = e.target
        // console.log(this.uploadIMG[i].url)
        // this.uploadIMG[i].url = this.getFileUrl(e.srcElement)
        this.regHeadshot[0].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.regHeadshot[0].file = files[0]
        this.regHeadshot[0].name = e.srcElement.files.item(0).name
        // $('#img_' + target.id).attr('src', this.getFileUrl(e.srcElement))
      }
    },
    cancelHeadshotIMG () {
      this.regHeadshot[0].file = this.regHeadshot[0].name = this.regHeadshot[0].url = ''
      $('#headshot-file').val('')
      // this.createImage(files[0])
    },
    regEmailFailMsg () {
      let isEmail = /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-|_)[A-Za-z0-9]+)*\.[A-Za-z]+$/
      if (this.submitVerify) {
        if (this.regEmail === '') {
          this.regEmailVerify = true
          return 'Email不得為空'
        } else if (!isEmail.test(this.regEmail)) {
          this.regEmailVerify = true
          return 'Email輸入格式錯誤'
        } else {
          this.regEmailVerify = false
          return '✔'
        }
      } else {
        this.regEmailVerify = false
        return '請輸入您的Email'
      }
    },
    regPwdFailMsg () {
      if (this.submitVerify) {
        if (this.regPwd.length < 8) {
          this.regPwdVerify = true
          return '密碼請勿少於8個字'
        } else {
          this.regPwdVerify = false
          return '✔'
        }
      } else {
        this.regPwdVerify = false
        return '請輸入至少8位數密碼'
      }
    },
    regPwdConfirmFailMsg () {
      if (this.submitVerify) {
        if (this.regPwdConfirm === '') {
          this.regPwdConfirmVerify = true
          return '請再次輸入密碼'
        } else if (this.regPwd.length < 8) {
          this.regPwdVerify = true
          return '密碼請勿少於8個字'
        } else if (this.regPwdConfirm !== this.regPwd) {
          this.regPwdConfirmVerify = true
          return '密碼輸入不一致'
        } else {
          this.regPwdConfirmVerify = false
          return '✔'
        }
      } else {
        this.regPwdConfirmVerify = false
        return '請再次輸入密碼'
      }
    },
    regMobileFailMsg () {
      let isMobile = /^09[0-9]{2}-[0-9]{6}$/
      if (this.submitVerify) {
        if (this.regMobile === '') {
          this.regMobileVerify = true
          return '手機號碼不得為空，格式為09XX-XXXXXX。僅作為緊急情況與您聯繫使用。'
        } else if (!isMobile.test(this.regMobile)) {
          this.regMobileVerify = true
          return '手機號碼輸入格式錯誤，格式為09XX-XXXXXX。僅作為緊急情況與您聯繫使用。'
        } else {
          this.regMobileVerify = false
          return '✔'
        }
      } else {
        this.regMobileVerify = false
        return '請輸入您的手機號碼，格式為09XX-XXXXXX。僅作為緊急情況與您聯繫使用。'
      }
    },
    regNameFailMsg () {
      if (this.submitVerify) {
        if (this.regName === '') {
          this.regNameVerify = true
          return '姓名不得為空'
        } else {
          this.regNameVerify = false
          return '✔'
        }
      } else {
        this.regNameVerify = false
        return '請輸入您的姓名'
      }
    },
    regNicknameFailMsg () {
      if (this.submitVerify) {
        if (this.regNickname !== '') {
          this.regNicknameVerify = false
          return '✔'
        } else {
          this.regNicknameVerify = false
          return '請輸入您的暱稱'
        }
      } else {
        this.regNicknameVerify = false
        return '請輸入您的暱稱'
      }
    },
    regRoleFailMsg () {
      if (this.submitVerify) {
        if (this.regRole === '') {
          this.regRoleVerify = true
          return '尚未選擇您的身分'
        } else {
          this.regRoleVerify = false
          return '✔'
        }
      } else {
        this.regRoleVerify = false
        return '請選擇您的身分'
      }
    },
    regGenderFailMsg () {
      if (this.submitVerify) {
        if (this.regGender === '') {
          this.regGenderVerify = true
          return '尚未選擇您的生理性別'
        } else {
          this.regGenderVerify = false
          return '✔'
        }
      } else {
        this.regGenderVerify = false
        return '請選擇您的生理性別'
      }
    },
    regBirthFailMsg () {
      let isBirth = /^[0-9]{4}-[0-9]{2}-[0-9]{2}$/
      if (this.submitVerify) {
        if (this.regBirth === '') {
          this.regBirthVerify = true
          return '生日不得為空，格式為1900-01-01'
        } else if (!isBirth.test(this.regBirth)) {
          this.regBirthVerify = true
          return '生日輸入格式錯誤，格式為1900-01-01'
        } else {
          this.regBirthVerify = false
          return '✔'
        }
      } else {
        this.regBirthVerify = false
        return '請輸入您的生日，格式為1900-01-01'
      }
    },
    regNotifiemailFailMsg () {
      if (this.submitVerify) {
        if (this.regNotifiemail !== '' && this.regNotifiemailArray()) {
          this.regNotifiemailVerify = true
          return 'Email輸入格式錯誤，格式為：you@cool.com;we@awesome.com'
        } else if (this.regNotifiemail !== '') {
          this.regNotifiemailVerify = false
          return '✔'
        } else {
          this.regNotifiemailVerify = false
          return '請用分號『;』隔開Email，如：you@cool.com;we@awesome.com'
        }
      } else {
        this.regNotifiemailVerify = false
        return '請用分號『;』隔開Email，如：you@cool.com;we@awesome.com'
      }
    },
    regNotifiemailArray (val) {
      let error = false
      let isNotifiemail = /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-|_)[A-Za-z0-9]+)*\.[A-Za-z]+$/
      let list = this.regNotifiemail.split(';')
      list.forEach((l) => {
        if (!isNotifiemail.test(l)) {
          error = true
        }
      })
      return error
    },
    dosubmitVerifyBefore () {
      this.submitVerify = true
    },
    doSubmitVerify () {
      if (!this.regEmailVerify &&
      !this.regPwdVerify &&
      !this.regPwdConfirmVerify &&
      !this.regMobileVerify &&
      !this.regNameVerify &&
      !this.regNicknameVerify &&
      !this.regRoleVerify &&
      !this.regGenderVerify &&
      !this.regBirthVerify &&
      !this.regNotifiemailVerify) {
        this.submit = true
      } else {
        this.submit = false
        scroll(0, 0)
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '請修正錯誤欄位後再送出'
      }
    },
    dosubmitVerifyAfter () {
      if (this.submit) {
        let formData = new FormData()
        formData.append('regEmail', this.regEmail)
        formData.append('regPwd', sha256(this.regPwd).toUpperCase())
        formData.append('regMobile', this.regMobile)
        formData.append('regName', this.regName)
        formData.append('regNickname', this.regNickname || this.regName)
        formData.append('regRole', this.regRole)
        formData.append('regGender', this.regGender)
        formData.append('regBirth', this.regBirth)
        formData.append('upload_snapshot', this.regHeadshot[0].file)
        formData.append('regNotifiemail', this.regNotifiemail)
        this.$http.post('api/account/signupStudent/', formData).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              scroll(0, 0)
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '註冊成功，請繼續完成驗證信箱以啟用帳號！'
              setTimeout(() => {
                this.$router.push('/')
              }, 3000)
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      }
    },
    async doSubmit () {
      await this.dosubmitVerifyBefore()
      await this.doSubmitVerify()
      await this.dosubmitVerifyAfter()
    }
  }
}
</script>
