      //   .then(res => {
      //     config.headers.common['X-CSRFtoken'] = res.csrftoken || 0
      //   })
      return config
    })
    this.$http.interceptors.response.use((response) => {
      // // if (response.data.errCode === 'E10000001' || response.data.errMsg === 'Access Token 驗證失敗') {
      // if (response.data.errCode === 'E10000001' || response.data.errCode === 'E10000002' || response.data.errCode === 'E10000003' || response.data.errCode === 'E10000004' || response.data.errCode === 'E10000005') {
