  mounted () {
    let publicPage = true
    if (!localStorage.getItem('token') && publicPage) {
      let body = {
        userId: this.userInfo ? this.userInfo.user_id : -1,
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              if (this.$route.params.nickname) {
                this.regNickname = this.$route.params.nickname
              }
              if (this.$route.params.male) {
                this.regGender = this.$route.params.male
              }
              if (this.$route.params.time) {
                this.timeID = this.$route.params.time
              }
              if (this.$route.params.random) {
                this.random = this.$route.params.random
              }
            } else {
              // this.$router.push('/')
            }
          } else {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else if (localStorage.getItem('token')) {
      this.$router.push('/')
    } else {
      // this.$router.push('/')
    }
  },
  methods: {
    setHeadshot (e) {
      let files = e.target.files || e.dataTransfer.files
      if (e.srcElement.files.item(0).size > (4 * 1024 * 1024)) {
        $('#headshot-file').val('')
        alert('上傳照片不得超過4MB')
      } else {
        // alert('Uploading: ' + e.srcElement.files.item(0).name + ' @ ' + e.srcElement.files.item(0).size + 'bytes')
        // console.log(window.URL.createObjectURL(e.srcElement.files.item(0)))
        // console.log(e.srcElement.files.item(0).name)
        // let target = e.target
        // console.log(this.uploadIMG[i].url)
        // this.uploadIMG[i].url = this.getFileUrl(e.srcElement)
        this.regHeadshot[0].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.regHeadshot[0].file = files[0]
        this.regHeadshot[0].name = e.srcElement.files.item(0).name
        // $('#img_' + target.id).attr('src', this.getFileUrl(e.srcElement))
      }
    },
    cancelHeadshotIMG () {
