    regEmailFailMsg () {
      let isEmail = /^\w+((-\w+)|(\.\w+))*@[A-Za-z0-9]+((\.|-|_)[A-Za-z0-9]+)*\.[A-Za-z]+$/
      if (this.submitVerify) {
        if (this.regEmail === '') {
          this.regEmailVerify = true
          return 'Email不得為空'
        } else if (!isEmail.test(this.regEmail)) {
          this.regEmailVerify = true
          return 'Email輸入格式錯誤'
        } else {
          this.regEmailVerify = false
          return '✔'
        }
      } else {
        this.regEmailVerify = false
        return '請輸入您的Email'
      }
    },
    regPwdFailMsg () {
      if (this.submitVerify) {
        if (this.regPwd.length < 8) {
          this.regPwdVerify = true
          return '密碼請勿少於8個字'
        } else {
          this.regPwdVerify = false
          return '✔'
        }
      } else {
        this.regPwdVerify = false
        return '請輸入至少8位數密碼'
      }
    },
    regPwdConfirmFailMsg () {
      if (this.submitVerify) {
        if (this.regPwdConfirm === '') {
          this.regPwdConfirmVerify = true
          return '請再次輸入密碼'
        } else if (this.regPwd.length < 8) {
          this.regPwdVerify = true
          return '密碼請勿少於8個字'
        } else if (this.regPwdConfirm !== this.regPwd) {
          this.regPwdConfirmVerify = true
          return '密碼輸入不一致'
        } else {
          this.regPwdConfirmVerify = false
          return '✔'
        }
      } else {
        this.regPwdConfirmVerify = false
        return '請再次輸入密碼'
      }
    },
    regMobileFailMsg () {
      let isMobile = /^09[0-9]{2}-[0-9]{6}$/
      if (this.submitVerify) {
        if (this.regMobile === '') {
          this.regMobileVerify = true
          return '手機號碼不得為空，格式為09XX-XXXXXX。僅作為緊急情況與您聯繫使用。'
        } else if (!isMobile.test(this.regMobile)) {
