    //   } else {
    //     this.regHeadshotVerify = false
    //     return '請選擇常態開課時段。'
    //   }
    // },
    dosubmitVerifyBefore () {
      this.submitVerify = true
    },
    doSubmitVerify () {
      if (!this.regEmailVerify &&
      !this.regPwdVerify &&
      !this.regPwdConfirmVerify &&
      !this.regMobileVerify &&
      !this.regNameVerify &&
      !this.regNicknameVerify &&
      !this.regGenderVerify &&
      !this.regYearVerify &&
      !this.regBirthVerify &&
      // !this.regBankVerify &&
      // !this.regBankAccountVerify &&
      !this.regIntroVerify &&
      !this.regTagVerify &&
      !this.regEdu1Verify &&
      !this.regEdu2Verify &&
      !this.regEdu3Verify &&
      !this.regJobVerify &&
      !this.regExpVerify) {
        this.submit = true
      } else {
        this.submit = false
        scroll(0, 0)
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '請修正錯誤欄位後再送出'
      }
    },
    dosubmitVerifyAfter () {
      if (this.submit) {
        let formData = new FormData()
        formData.append('dummy_teacher_id', (this.timeID !== '') ? (this.timeID + this.regNickname + this.random) : '')
        formData.append('regEmail', this.regEmail)
        formData.append('regPwd', sha256(this.regPwd).toUpperCase())
        formData.append('regMobile', this.regMobile)
        formData.append('regName', this.regName)
        formData.append('regNickname', this.regNickname || this.regName)
        formData.append('regGender', this.regGender)
        // formData.append('registered_bank', this.regBank)
        // formData.append('registered_bank_account', this.regBankAccount)
        formData.append('tutor_experience', this.regYear)
        formData.append('regBirth', this.regBirth)
        formData.append('intro', this.regIntro)
        formData.append('subject_type', this.regTag)
        formData.append('education_1', this.regEdu1)
        formData.append('education_2', this.regEdu2)
        formData.append('education_3', this.regEdu3)
        formData.append('company', this.regJob)
        formData.append('special_exp', this.regExp)
        formData.append('teacher_general_availabale_time', this.regSelectTime)
        formData.append('upload_snapshot', this.regHeadshot[0].file)
        this.uploadIMG.forEach((up) => {
          formData.append('upload_cer', up.file)
        })
        this.$http.post('api/account/signupTeacher/', formData).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              scroll(0, 0)
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '您的申請將交由專人審核，於1~3日內通知審核結果，請您密切注意信箱！'
              setTimeout(() => {
                this.$router.push('/')
              }, 3000)
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      }
    },
    async doSubmit () {
      await this.dosubmitVerifyBefore()
      await this.doSubmitVerify()
      await this.dosubmitVerifyAfter()
    }
  }
}
</script>
