  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  mounted () {
    // console.log(document.referrer)
    // console.log(window.document.referrer)
    this.doMounted()
  },
  methods: {
    test () {
      alert('未開放')
    },
    doMounted () {
      // console.log(document.referrer)
      // console.log(window.document.referrer)
      let publicPage = false
      if (this.$route.params.action === 'preview' && this.userInfo !== null) {
        if (this.userInfo.type === 'teacher') {
          publicPage = true
        }
      } else if (this.$route.params.action === 'view') {
        publicPage = true
      }
      if (localStorage.getItem('token') || publicPage) {
        let body = {
          userId: this.userInfo ? this.userInfo.user_id : -1,
          url: this.$route.path
        }
        this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              if (res.data.authority) {
                if (this.$route.params.action) {
                  this.action = this.$route.params.action
                }
                if (this.$route.params.id) {
                  this.lessonId = this.$route.params.id
                }
                this.getLessonInfo()
              } else {
                // this.$router.push('/')
              }
            } else {
              this.$store.dispatch('logout').then(() => {
                this.$router.go(0)
              })
            }
          })
      } else {
        this.$router.push('/')
      }
    },
    getTeacherProfile (id) {
      this.$http.get(`/api/account/returnTeacherProfileForPublicViewing/?userID=${id}&type=teacher`).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            this.pageView = true
            this.teacherApproved = res.data.id_approved
            // this.teacherAvgScore = res.data.all_lesson_score_mean
            // this.teacherReviewedTimes = res.data.total_number_of_remark
            this.teacherEdu1 = res.data.education_1
            this.teacherEdu2 = res.data.education_2
            this.teacherEdu3 = res.data.education_3
            this.teacherEduApproved = res.data.education_approved
            this.teacherJob = res.data.company
            this.teacherJobApproved = res.data.work_approved
            this.teacherExp = res.data.special_exp
            this.teacherExpApproved = res.data.other_approved
            this.teacherAllApproved = res.data.is_approved
            this.teacherNickname = res.data.nickname
            this.teacherYear = res.data.tutor_experience
            // this.teacherIntro = res.data.intro
            // this.teacherTag = res.data.subject_type
            this.teacherGender = res.data.is_male ? boyIcon : girlIcon
            if (res.data.thumbnail_dir !== '') {
              this.teacherPhoto = res.data.thumbnail_dir
            } else {
              if (res.data.is_male) {
                this.teacherPhoto = this.boyTeacher
              } else {
                this.teacherPhoto = this.girlTeacher
              }
            }
            this.lessonDateDefault = res.data.available_time || []
            this.lessonDateBooked = res.data.booked_time || []
          } else {
            this.$router.go(-1)
          }
        })
    },
    getLessonInfo () {
      let url = ((this.action === 'view') ? '/api/lesson/returnLessonDetailsForBrowsing/?action=browsing' : ((this.action === 'preview') ? 'api/lesson/returnLessonDetailsForTeacherWhoCreatedIt/?action=editLesson' : ''))
      this.$http.get(`${url}&lessonID=${this.lessonId}&userID=${this.userInfo ? this.userInfo.user_id : -1}`).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (this.action === 'preview' || res.data.selling_status === 'selling') {
              // this.readyGender = res.data.is_this_teacher_male
              this.lessonFavorite = res.data.is_this_my_favorite_lesson
              this.lessonTitle = res.data.big_title // 大標題
              this.lessonSubtitle = res.data.little_title // 小標題
              this.lessonTextcolor = res.data.title_color
              this.lessonCardBg = res.data.background_picture_code
              if (res.data.background_picture_code === 99) {
                this.lessonCardBgPath = res.data.background_picture_path
              } else if (res.data.background_picture_code === 0) {
                this.lessonCardBgPath = this.cardBg1
              } else if (res.data.background_picture_code === 1) {
                this.lessonCardBgPath = this.cardBg2
              } else if (res.data.background_picture_code === 2) {
                this.lessonCardBgPath = this.cardBg3
              }
              this.teacherId = res.data.teacher_auth_id
              this.lessonName = res.data.lesson_title // 課程名稱
              this.lessonFee = res.data.price_per_hour // 鐘點費
              this.lessonSpecially1 = res.data.highlight_1 // 亮點
              this.lessonSpecially2 = res.data.highlight_2
              this.lessonSpecially3 = res.data.highlight_3
              this.lessonIntro = res.data.lesson_intro // 課程介紹
              this.lessonWay = res.data.how_does_lesson_go // 上課方式
              this.lessonObject = res.data.target_students // 學生類型
              this.lessonMemo = res.data.lesson_remarks // 備註
              this.lessonOutline = res.data.syllabus
              // if (res.data.syllabus !== '' || res.data.syllabus !== null) {
              //   let outline = res.data.syllabus.split('<div class="area"><div class="chapter"><div>')
              //   outline.forEach((c, chapter) => {
              //     if (chapter > 0) {
              //       this.lessonOutline.push({ type: 'chapter', text: '', content: [] })
              //       let subsection = c.split('</div></div><div class="subsection"><div>')
              //       subsection.forEach((s, index) => {
              //         let text = s.replace('</div></div></div>', '').split('</div><div>')
              //         if (index === 0) {
              //           this.lessonOutline[chapter - 1].text = text[1]
              //         } else {
              //           this.lessonOutline[chapter - 1].content.push({ type: 'subsection', text: text[1] })
              //         }
              //       })
              //     }
              //   })
              // }
              this.lessonTag = res.data.lesson_attributes // 標籤
              this.lessonSaleTry = (Number(res.data.trial_class_price) >= 0) // 試課費用
              this.lessonSaleFeeTry = (Number(res.data.trial_class_price) >= 0) ? Number(res.data.trial_class_price) : 200
              // this.lessonSaleUnit = (Number(res.data.unitClassPrice) > 0)
              this.lessonSaleUnit = res.data.lesson_has_one_hour_package // 單節方案
              if (this.action === 'preview') {
                this.getTeacherProfile(this.userInfo ? this.userInfo.user_id : -1)
              } else {
                this.getTeacherProfile(res.data.teacher_auth_id)
              }
              if (res.data.discount_price !== '' && res.data.discount_price !== null) {
                let discountPrice = res.data.discount_price.split(';')
                this.lessonSale = []
                discountPrice.forEach((d, index) => {
                  if (d !== '') {
                    let dd = d.split(':')
                    this.lessonSale.push({ hour: dd[0], off: dd[1] })
                  }
                })
              }
              setTimeout(() => {
                if (this.$router.history.current.path.indexOf('/lesson/main') !== -1) {
                  this.getAverageRGB()
                }
              }, 500)
            } else {
              this.$router.push('/')
            }
          } else {
            // this.snackbar = true
            // this.color = 'red'
            // this.errorMessages = res.errMsg
            this.$router.go(-1)
          }
        })
    },
    getAverageRGB () {
      let imgEl = document.getElementById('lessonbg')
      let blockSize = 5 // only visit every 5 pixels
      let defaultRGB = { r: 0, g: 0, b: 0 } // for non-supporting envs
      let canvas = document.createElement('canvas')
      let context = canvas.getContext && canvas.getContext('2d')
      let data, width, height
      let i = -4
      let length
      let count = 0

      if (!context) {
        return defaultRGB
      }

      height = canvas.height = imgEl.naturalHeight || imgEl.offsetHeight || imgEl.height
      width = canvas.width = imgEl.naturalWidth || imgEl.offsetWidth || imgEl.width

      context.drawImage(imgEl, 0, 0)

      try {
        data = context.getImageData(0, 0, width, height)
      } catch (e) {
        // alert('x')
        return defaultRGB
      }

      length = data.data.length

      while ((i += blockSize * 4) < length) {
        ++count
        this.rgb.r += data.data[i]
        this.rgb.g += data.data[i + 1]
        this.rgb.b += data.data[i + 2]
      }
      // ~~ used to floor values
      this.rgb.r = ~~(this.rgb.r / count)
      this.rgb.g = ~~(this.rgb.g / count)
      this.rgb.b = ~~(this.rgb.b / count)
    },
    doFavorite (id, status) {
      if (localStorage.getItem('token')) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          lessonID: id,
          status: status
        }
        this.$http.post('/api/lesson/addOrRemoveFavoriteLessons/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.getLessonInfo()
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '已' + (status ? '加入' : '移除') + '收藏'
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    doConnectChatroom () {
      if (localStorage.getItem('token')) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          chatUserID: this.teacherId
        }
        this.$http.post('/api/chat/checkOrCreateChatroom/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              // this.snackbar = true
              // this.color = 'green'
              // this.errorMessages = '已開啟聊天室'
              this.$store.state.doChatView = true
              // this.$store.state.doChatAction = 'message'
              this.$store.state.doChatroomID = res.data.chatID
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.$store.state.dologinView = true
      }
    },
    // 定義複製成功的回傳方法
    clipboardSuccessHandler ({ value, event }) {
      if (this.action === 'preview') {
        alert('請注意！若課程為非上架狀態，其他會員及訪客將無法瀏覽')
      }
      // console.log('success', value)
      this.snackbar = true
      this.color = 'green'
      this.errorMessages = '已複製網址'
    },
    // 定義複製失敗的回調
    clipboardErrorHandler ({ value, event }) {
      // console.log('error', value)
      // this.snackbar = true
      // this.color = 'red'
      // this.errorMessages = '無法複製網址'
    }
  }
}
</script>
