                <span>小時，<br>提供總價</span>
                <input
                  id="readySaleOff3"
                  v-model="readySaleOff3"
                  type="text"
                  persistent-hint>%
                <span>優惠價</span>
              </div>
              <div>
                <span>原價</span>
                <strong>{{ readyFee * readySaleHour3 }}</strong>
                <span>元，<br>特價</span>
                <div class="mx-1 text-danger d-inline-block">{{ Math.ceil(readyFee * readySaleHour3 * readySaleOff3 / 100) }}</div>
                <span>元</span>
              </div>
            </h3>
          </v-radio-group>
        </div>
      </div>
      <hr>
      <div class="row">
        <div class="col-xs-12 col-md-12 text-center">
          <v-btn
            v-if="sellStatus==='draft' || sellStatus==='notSelling'"
            class="mx-4 my-4 p-4 h5 bg-button-color"
            @click="doSubmit('selling')">
            <v-icon class="mr-2">mdi-upload</v-icon>儲存課程並上架
          </v-btn>
          <v-btn
            v-if="sellStatus==='selling' || sellStatus==='notSelling'"
            class="mx-4 my-4 p-4 h5 bg-grey-color"
            @click="doSubmit(sellStatus)">
            <v-icon class="mr-2">mdi-content-save</v-icon>儲存課程
          </v-btn>
          <v-btn
            v-if="sellStatus==='draft'"
            class="mx-4 my-4 p-4 h5 bg-grey-color"
            @click="doSubmit('draft')">
            <v-icon class="mr-2">mdi-content-save</v-icon>儲存課程草稿
          </v-btn>
        </div>
        <!-- <pre style="word-wrap: break-word; white-space: pre-wrap;">{{ msg }}</pre> -->
      </div>
    </div>
    <v-snackbar
      v-model="snackbar"
      :top="true"
      :color="color">
      {{ errorMessages }}
      <v-btn
        dark
        flat
        @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import $ from 'jquery'
import qs from 'qs'
import { mapState } from 'vuex'
import prove from '@/assets/img/prove.png'
import cardBg1 from '@/assets/img/lesson_card_bg1.jpg'
import cardBg2 from '@/assets/img/lesson_card_bg2.jpg'
import cardBg3 from '@/assets/img/lesson_card_bg3.jpg'
// import ToolbarVue from '../core/Toolbar.vue'
// import TimeSelector from '../Plugins/TimeSelector.vue'
export default {
  name: 'LessonReady',
  // components: {
  //   TimeSelector
  // },
  metaInfo () {
    return {
      title: this.appName + '｜' + (this.action === 'edit' ? '編輯' : '新增') + '課程'
    }
  },
  data: function () {
    return {
      pageView: false,
      action: '',
      prove,
      cardBg1,
      cardBg2,
      cardBg3,
      snackbar: false,
      color: 'red',
      errorMessages: '',
      readyId: null,
      sellStatus: 'draft',
      readyCardBg: 0,
      readyCardBgList: [cardBg1, cardBg2, cardBg3, ''],
      readyTitle: '',
      readyTitleMessages: '0/10字',
      readySubtitle: '',
      readySubtitleMessages: '0/10字',
      readyTextcolor: '#ffffff',
      readybestSale: '',
      readyName: '',
      readyNameMessages: '0/14字',
      readyScore: 0,
      readyReviewed: 0,
      readyFee: '',
      readyFeeMessages: '請輸入課程鐘點費',
      readySpecially1: '',
      readySpecially1Messages: '0/10字',
      readySpecially2: '',
      readySpecially2Messages: '0/10字',
      readySpecially3: '',
      readySpecially3Messages: '0/10字',
      readyIntro: '',
      readyIntroMessages: '請輸入課程介紹',
      readyWay: '',
      readyWayMessages: '請輸入上課方式',
      readyObject: '',
      readyObjectMessages: '請輸入授課對象，如：年齡限制、建議職業...等',
      readyMemo: '',
      readyMemoMessages: '請輸入課程注意事項',
      readyTag: '',
      readyTagMessages: '請輸入課程關鍵字',
      readyCardBgUpload: [
        {
          file: '',
          name: '',
          url: ''
        }
      ],
      readyOutline: [
        {
          type: 'chapter',
          text: '',
          content: [
            {
              type: 'subsection',
              text: ''
            }
          ]
        }
      ],
      readyOutlineReadOnly: '',
      readySaleUnit: true,
      readySaleTry: true,
      readySaleFeeTry: 200,
      readySale1: true,
      readySaleHour1: 10,
      readySaleOff1: 95,
      readySale2: true,
      readySaleHour2: 20,
      readySaleOff2: 80,
      readySale3: true,
      readySaleHour3: 30,
      readySaleOff3: 70,
      // readyHeadshotVerify: false,
      readySelectTime: [],
      // readySelectTimeVerify: false,
      userAgreementOpen: false,
      userAgreementCheck: false,
      submitVerify: false,
      submit: false
    }
  },
  computed: {
    ...mapState(['root', 'appName']),
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
  },
  watch: {
    '$route.fullPath': function (newValue) {
      this.$router.go(0)
    },
    readyTitle (val) {
      if (val.length > 10) {
        this.readyTitleMessages = '✖ ' + val.length + '/10字'
      } else if (val.length === 0) {
        this.readyTitleMessages = '0/10字'
      } else {
        this.readyTitleMessages = '✔ ' + val.length + '/10字'
      }
    },
    readySubtitle (val) {
      if (val.length > 10) {
        this.readySubtitleMessages = '✖ ' + val.length + '/10字'
      } else if (val.length === 0) {
        this.readySubtitleMessages = '0/10字'
      } else {
        this.readySubtitleMessages = '✔ ' + val.length + '/10字'
      }
    },
    readyName (val) {
      if (val.length === 0) {
        this.readyNameMessages = '✖ ' + val.length + '/14字'
      } else if (val.length > 14) {
        this.readyNameMessages = '✖ ' + val.length + '/14字'
      } else {
        this.readyNameMessages = '✔ ' + val.length + '/14字'
      }
    },
    readyFee (val) {
      let isFee = /^[0-9]+$/
      if (val === '') {
        this.readyFeeMessages = '✖ 課程鐘點費不得為空'
      } else if (!isFee.test(this.readyFee)) {
        this.readyFeeMessages = '✖ 課程鐘點費輸入格式錯誤'
      } else {
        this.readyFeeMessages = '✔'
        this.readyFee = Number(this.readyFee)
      }
    },
    readySpecially1 (val) {
      if (val.length > 10) {
        this.readySpecially1Messages = '✖ ' + val.length + '/10字'
      } else if (val.length === 0) {
        this.readySpecially1Messages = '0/10字'
      } else {
        this.readySpecially1Messages = '✔ ' + val.length + '/10字'
      }
    },
    readySpecially2 (val) {
      if (val.length > 10) {
        this.readySpecially2Messages = '✖ ' + val.length + '/10字'
      } else if (val.length === 0) {
        this.readySpecially2Messages = '0/10字'
      } else {
        this.readySpecially2Messages = '✔ ' + val.length + '/10字'
      }
    },
    readySpecially3 (val) {
      if (val.length > 10) {
        this.readySpecially3Messages = '✖ ' + val.length + '/10字'
      } else if (val.length === 0) {
        this.readySpecially3Messages = '0/10字'
      } else {
        this.readySpecially3Messages = '✔ ' + val.length + '/10字'
      }
    },
    readyIntro (val) {
      if (val === '') {
        this.readyIntroMessages = '✖ 課程介紹不得為空'
      } else {
        this.readyIntroMessages = '✔'
      }
    },
    readyWay (val) {
      if (val === '') {
        this.readyWayMessages = '✖ 上課方式不得為空'
      } else {
        this.readyWayMessages = '✔'
      }
    },
    readyObject (val) {
      if (val === '') {
        this.readyObjectMessages = '請輸入授課對象，如：年齡限制、建議職業...等'
      } else {
        this.readyObjectMessages = '✔'
      }
    },
    readyMemo (val) {
      if (val === '') {
        this.readyMemoMessages = '請輸入課程注意事項'
      } else {
        this.readyMemoMessages = '✔'
      }
    },
    readyTag (val) {
      if (val === '') {
        this.readyTagMessages = '請輸入課程關鍵字'
      } else {
        this.readyTagMessages = '✔'
      }
    },
    readySaleFeeTry (val) {
      if (val > 9999) {
        this.readySaleFeeTry = 9999
      } else if (val < 0) {
        this.readySaleFeeTry = 0
      } else {
        this.readySaleFeeTry = this.readySaleFeeTry.replace(/[^0-9]/ig, '')
      }
    },
    readySaleHour1 (val) {
      if (val > 999) {
        this.readySaleHour1 = 999
      } else if (val < 1) {
        this.readySaleHour1 = 1
      } else {
        this.readySaleHour1 = this.readySaleHour1.replace(/[^0-9]/ig, '')
      }
    },
    readySaleOff1 (val) {
      if (val > 99) {
        this.readySaleOff1 = 99
      } else if (val < 1) {
        this.readySaleOff1 = 1
      } else {
        this.readySaleOff1 = this.readySaleOff1.replace(/[^0-9]/ig, '')
      }
    },
    readySaleHour2 (val) {
      if (val > 999) {
        this.readySaleHour2 = 999
      } else if (val < 1) {
        this.readySaleHour2 = 1
      } else {
        this.readySaleHour2 = this.readySaleHour2.replace(/[^0-9]/ig, '')
      }
    },
    readySaleOff2 (val) {
      if (val > 99) {
        this.readySaleOff2 = 99
      } else if (val < 1) {
        this.readySaleOff2 = 1
      } else {
        this.readySaleOff2 = this.readySaleOff2.replace(/[^0-9]/ig, '')
      }
    },
    readySaleHour3 (val) {
      if (val > 999) {
        this.readySaleHour3 = 999
      } else if (val < 1) {
        this.readySaleHour3 = 1
      } else {
        this.readySaleHour3 = this.readySaleHour3.replace(/[^0-9]/ig, '')
      }
    },
    readySaleOff3 (val) {
      if (val > 99) {
        this.readySaleOff3 = 99
      } else if (val < 1) {
        this.readySaleOff3 = 1
      } else {
        this.readySaleOff3 = this.readySaleOff3.replace(/[^0-9]/ig, '')
      }
    }
  },
  mounted () {
    if (localStorage.getItem('token')) {
      let body = {
        userId: this.userInfo ? this.userInfo.user_id : -1,
        url: this.$route.path
      }
      this.$http.post('/api/account/authCheck/', qs.stringify(body)).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (res.data.authority) {
              this.getReadyOutlineReadOnly()
              if (this.$route.params.action) {
                this.action = this.$route.params.action
              }
              if (this.$route.params.id) {
                this.readyId = this.$route.params.id
              }
              if (this.action === 'edit') {
                this.getLessonInfo()
              } else if (this.action === 'add') {
                this.pageView = true
              }
            } else {
              this.$router.push('/')
            }
          } else {
            this.$store.dispatch('logout').then(() => {
              this.$router.go(0)
            })
          }
        })
    } else {
      this.$router.push('/')
    }
  },
  methods: {
    getLessonInfo () {
      this.$http.get(`/api/lesson/returnLessonDetailsForTeacherWhoCreatedIt/?action=editLesson&lessonID=${this.readyId}&userID=${this.userInfo.user_id}`).then(res => res.data)
        .then(res => {
          if (res.status === 'success') {
            if (this.userInfo.type === 'teacher') {
              this.pageView = true
              // this.readyGender = res.data.is_this_teacher_male
              this.readyTitle = res.data.big_title // 大標題
              this.readySubtitle = res.data.little_title // 小標題
              this.readyTextcolor = res.data.title_color
              this.readybestSale = res.data.best_sale
              this.readyCardBg = res.data.background_picture_code
              if (res.data.background_picture_code === 99) {
                this.readyCardBgUpload[0].url = res.data.background_picture_path
                this.readyCardBgUpload[0].file = ''
                this.readyCardBgUpload[0].name = ''
                this.readyCardBgList[3] = res.data.background_picture_path
              }
              this.readyName = res.data.lesson_title // 課程名稱
              this.readyFee = res.data.price_per_hour // 鐘點費
              this.readySpecially1 = res.data.highlight_1 // 亮點
              this.readySpecially2 = res.data.highlight_2
              this.readySpecially3 = res.data.highlight_3
              this.readyIntro = res.data.lesson_intro // 課程介紹
              this.readyWay = res.data.how_does_lesson_go // 上課方式
              this.readyObject = res.data.target_students // 學生類型
              this.readyMemo = res.data.lesson_remarks // 備註
              this.readyScore = res.data.lesson_avg_score
              this.readyReviewed = res.data.lesson_reviewed_times

              if (res.data.syllabus !== '' || res.data.syllabus !== null) {
                this.readyOutline = []
                let outline = res.data.syllabus.split('<div class="area"><div class="chapter"><div>')
                outline.forEach((c, chapter) => {
                  if (chapter > 0) {
                    this.readyOutline.push({ type: 'chapter', text: '', content: [] })
                    let subsection = c.split('</div></div><div class="subsection"><div>')
                    subsection.forEach((s, index) => {
                      let text = s.replace('</div></div></div>', '').split('</div><div>')
                      if (index === 0) {
                        this.readyOutline[chapter - 1].text = text[1]
                      } else {
                        this.readyOutline[chapter - 1].content.push({ type: 'subsection', text: text[1] })
                      }
                    })
                  }
                })
              }
              this.sellStatus = res.data.selling_status
              this.readyTag = res.data.lesson_attributes // 標籤
              this.readySaleTry = (Number(res.data.trial_class_price) >= 0) // 試課費用
              this.readySaleFeeTry = (Number(res.data.trial_class_price) >= 0) ? Number(res.data.trial_class_price) : 200
              // this.readySaleUnit = (Number(res.data.unitClassPrice) > 0)
              this.readySaleUnit = res.data.lesson_has_one_hour_package // 單節方案
              this.readySale1 = false
              this.readySale2 = false
              this.readySale3 = false
              if (res.data.discount_price !== '' && res.data.discount_price !== null) {
                let discountPrice = res.data.discount_price.split(';')
                discountPrice.forEach((d, index) => {
                  if (d !== '') {
                    let dd = d.split(':')
                    if (index === 0) {
                      this.readySale1 = true
                      this.readySaleHour1 = dd[0]
                      this.readySaleOff1 = dd[1]
                    } else if (index === 1) {
                      this.readySale2 = true
                      this.readySaleHour2 = dd[0]
                      this.readySaleOff2 = dd[1]
                    } else if (index === 2) {
                      this.readySale3 = true
                      this.readySaleHour3 = dd[0]
                      this.readySaleOff3 = dd[1]
                    }
                  }
                })
              }
            } else {
              this.$router.push('/')
            }
          } else {
            if (this.userInfo.type === 'teacher') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
              this.$router.go(-1)
            } else {
              this.$router.push('/')
            }
          }
        })
    },
    doReadyOutlineSort (action, type, c, s) {
      let direction = (action === 'up' ? -1 : action === 'down' ? 1 : 0)
      if (type === 'c') {
        let array = [
          this.readyOutline[c].text,
          this.readyOutline[c].content,
          this.readyOutline[(c + direction)].text,
          this.readyOutline[(c + direction)].content
        ]
        this.readyOutline[c].text = array[2]
        this.readyOutline[c].content = array[3]
        this.readyOutline[(c + direction)].text = array[0]
        this.readyOutline[(c + direction)].content = array[1]
      } else if (type === 's') {
        let array = [
          this.readyOutline[c].content[s].text,
          this.readyOutline[c].content[(s + direction)].text
        ]
        this.readyOutline[c].content[s].text = array[1]
        this.readyOutline[c].content[(s + direction)].text = array[0]
      }
      setTimeout(() => {
        this.getReadyOutlineReadOnly()
      }, 0)
    },
    doReadyOutlineAdd (type, c) {
      if (type === 'c') {
        let chapter = {
          type: 'chapter',
          text: '',
          content: [
            {
              type: 'subsection',
              text: ''
            }
          ]
        }
        this.readyOutline.push(chapter)
      } else if (type === 's') {
        let subsection = {
          type: 'subsection',
          text: ''
        }
        this.readyOutline[c].content.push(subsection)
      }
      setTimeout(() => {
        this.getReadyOutlineReadOnly()
      }, 0)
    },
    doReadyOutlineRemove (type, c, s) {
      if (type === 'c') {
        this.readyOutline.splice(c, 1)
      } else if (type === 's') {
        this.readyOutline[c].content.splice(s, 1)
      }
      setTimeout(() => {
        this.getReadyOutlineReadOnly()
      }, 0)
    },
    getReadyOutlineReadOnly () {
      let text = ''
      this.readyOutline.forEach((chapter, ch) => {
        text = text + '<div class="area"><div class="chapter"><div>第' + (ch + 1) + '章</div><div>' + ($('#chapter' + ch).val() || '') + '</div></div>'
        chapter.content.forEach((subsection, sub) => {
          text = text + '<div class="subsection"><div>' + (ch + 1) + '-' + (sub + 1) + '</div><div>' + ($('#subsection' + ch + '_' + sub).val() || '') + '</div></div>'
        })
        text = text + '</div>'
      })
      this.readyOutlineReadOnly = text
    },
    selectReadyCardBg () {
      if (this.readyCardBgUpload[0].url) {
        this.readyCardBg = 99
      }
    },
    uploadReadyCardBg () {
      if (!this.readyCardBgUpload[0].url) {
        $('#file_readyCardBg').click()
      }
    },
    setReadyCardBg (e) {
      let files = e.target.files || e.dataTransfer.files
      if (e.srcElement.files.item(0).size > (4 * 1024 * 1024)) {
        $('#file_readyCardBg').val('')
        alert('上傳照片不得超過4MB')
      } else {
        // alert('Uploading: ' + e.srcElement.files.item(0).name + ' @ ' + e.srcElement.files.item(0).size + 'bytes')
        // console.log(window.URL.createObjectURL(e.srcElement.files.item(0)))
        // console.log(e.srcElement.files.item(0).name)
        // let target = e.target
        // console.log(this.uploadIMG[i].url)
        // this.uploadIMG[i].url = this.getFileUrl(e.srcElement)
        this.readyCardBgUpload[0].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.readyCardBgUpload[0].file = files[0]
        this.readyCardBgUpload[0].name = e.srcElement.files.item(0).name
        this.readyCardBgList[3] = this.readyCardBgUpload[0].url
        // $('#img_' + target.id).attr('src', this.getFileUrl(e.srcElement))
      }
    },
    cancelReadyCardBg () {
      this.readyCardBgUpload[0].file = this.readyCardBgUpload[0].name = this.readyCardBgUpload[0].url = ''
      this.readyCardBgList[3] = ''
      $('#file_readyCardBg').val('')
      // this.createImage(files[0])
    },
    // dosubmitVerifyBefore () {
    //   this.submitVerify = true
    // },
    doSubmitVerify () {
      this.getReadyOutlineReadOnly()
      if (this.readyTitleMessages.indexOf('✖') === -1 &&
      this.readySubtitleMessages.indexOf('✖') === -1 &&
      this.readyName !== '' &&
      this.readyNameMessages.indexOf('✖') === -1 &&
      this.readyFee !== '' &&
      this.readyFeeMessages.indexOf('✖') === -1 &&
      this.readySpecially1Messages.indexOf('✖') === -1 &&
      this.readySpecially2Messages.indexOf('✖') === -1 &&
      this.readySpecially3Messages.indexOf('✖') === -1 &&
      this.readyIntro !== '' &&
      this.readyIntroMessages.indexOf('✖') === -1 &&
      this.readyWay !== '' &&
      this.readyWayMessages.indexOf('✖') === -1 &&
      this.readyObjectMessages.indexOf('✖') === -1 &&
      this.readyMemoMessages.indexOf('✖') === -1 &&
      this.readyTagMessages.indexOf('✖') === -1) {
        if (this.readySaleUnit || (this.readySale1 || this.readySale2 || this.readySale3)) {
          this.submit = true
        } else {
          this.submit = false
          this.snackbar = true
          this.color = 'red'
          this.errorMessages = '請至少選擇單節販售或方案一至三其中一項'
        }
      } else {
        this.submit = false
        scroll(0, 0)
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = '請再次檢查欄位是否填妥'
      }
    },
    dosubmitVerifyAfter (status) {
      if (this.submit) {
        let discountPrice = ''
        if (this.readySale1) {
          discountPrice = discountPrice + this.readySaleHour1 + ':' + this.readySaleOff1 + ';'
        }
        if (this.readySale2) {
          discountPrice = discountPrice + this.readySaleHour2 + ':' + this.readySaleOff2 + ';'
        }
        if (this.readySale3) {
          discountPrice = discountPrice + this.readySaleHour3 + ':' + this.readySaleOff3 + ';'
        }
        let formData = new FormData()
        formData.append('action', this.action === 'edit' ? 'editLesson' : this.action === 'add' ? 'createLesson' : '')
        formData.append('selling_status', status)
        formData.append('lessonID', this.readyId || null)
        formData.append('userID', this.userInfo.user_id)
        formData.append('big_title', this.readyTitle)
        formData.append('little_title', this.readySubtitle)
        formData.append('title_color', this.readyTextcolor)
        formData.append('background_picture_code', this.readyCardBg)
        formData.append('lesson_title', this.readyName)
        formData.append('price_per_hour', this.readyFee)
        formData.append('highlight_1', this.readySpecially1)
        formData.append('highlight_2', this.readySpecially2)
        formData.append('highlight_3', this.readySpecially3)
        formData.append('lesson_intro', this.readyIntro)
        formData.append('how_does_lesson_go', this.readyWay)
        formData.append('target_students', this.readyObject)
        formData.append('lesson_remarks', this.readyMemo)
        formData.append('lesson_attributes', this.readyTag)
        formData.append('syllabus', this.readyOutlineReadOnly)
        formData.append('trial_class_price', this.readySaleTry ? this.readySaleFeeTry : -999)
        // formData.append('unitClassPrice', this.readySaleUnit ? this.readyFee : null)
        formData.append('lesson_has_one_hour_package', this.readySaleUnit)
        formData.append('discount_price', discountPrice)
        formData.append('background_picture_path', (this.readyCardBg === 99 && this.readyCardBgUpload[0].file !== '') ? this.readyCardBgUpload[0].file : '')
        // let path = this.action === 'add' ? 'api/lesson/createlesson/' : this.action === 'edit' ? 'api/lesson/editlesson/' : ''
        this.$http.post('api/lesson/createOrEditLesson/', formData).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = (this.action === 'edit' ? '編輯' : '新增') + '課程成功!'
              this.$router.push('/account/lesson')
            } else if (res.status === 'failed') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = '發生錯誤，請稍後再試'
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = '發生錯誤，請稍後再試'
            console.log(err)
          })
      }
    },
    async doSubmit (status) {
      // await this.dosubmitVerifyBefore()
      await this.doSubmitVerify()
      await this.dosubmitVerifyAfter(status)
    }
  }
}
</script>
